import type {HTMLAttributes, ReactNode} from 'react'
import React, {useCallback, useMemo} from 'react'
import * as Tooltip from '@radix-ui/react-tooltip'

import {useBoolean} from '@bpinc/lib-helpers'
import {IconAddToCopy, IconCopy} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {getMessageId} from '../../../helpers'
import {MessageAction} from '../../MessageAction'
import {
    useChatGetClipboardBuffer,
    useChatHasSelection,
    useChatMessages,
    useChatScrollContainer,
    useChatSetClipboardBuffer,
} from './ChatMessagesContext'
import {useGetFormattedClipboardMessage} from './useGetFormattedClipboardMessage'


interface WithHoverOverActionsProps extends HTMLAttributes<HTMLElement> {
    messageId: string
    children: ReactNode
}

export const WithHoverOverActions: React.FC<WithHoverOverActionsProps> = ({children, messageId, ...triggerHtmlProps}) => {
    const getFormattedClipboardMessage = useGetFormattedClipboardMessage()
    const hasSelection = useChatHasSelection()
    const messages = useChatMessages()
    const getClipboardBuffer = useChatGetClipboardBuffer()
    const setClipboardBuffer = useChatSetClipboardBuffer()
    const scrollContainer = useChatScrollContainer()
    const message = useMemo(() => messages.find(m => getMessageId(m) === messageId), [messageId, messages])
    const t = useLocalizationState('t')
    const open = useBoolean()

    const handleCopyToClipboard = useCallback(() => {
        if (!message) {
            return
        }

        const result = getFormattedClipboardMessage(message)

        console.info(' Clipboard text\n', result)
        navigator.clipboard.writeText(result)
        setClipboardBuffer(result)
        open.setFalse()
    }, [getFormattedClipboardMessage, message, open, setClipboardBuffer])

    const handleAddToClipboard = useCallback(() => {
        if (!message) {
            return
        }
        const result = getFormattedClipboardMessage(message)
        const stored = getClipboardBuffer()
        const newClipboardData = stored + result
        navigator.clipboard.writeText(newClipboardData)
        setClipboardBuffer(newClipboardData)
        console.info(' Clipboard text\n', newClipboardData)
        open.setFalse()
    }, [getClipboardBuffer, getFormattedClipboardMessage, message, open, setClipboardBuffer])


    return (
        <Tooltip.Root open={hasSelection ? false : open.value} onOpenChange={open.toggle} >
            <Tooltip.Trigger asChild>
                <div {...triggerHtmlProps}>
                    {children}
                </div>
            </Tooltip.Trigger>
            <Tooltip.Portal container={scrollContainer}>
                <Tooltip.Content collisionBoundary={scrollContainer} side="top" align="end" className="z-10 flex gap-1" hideWhenDetached>
                    <MessageAction onClick={handleCopyToClipboard}>
                        <IconCopy />
                        {t.GLOBAL.copy()}
                    </MessageAction>
                    <MessageAction onClick={handleAddToClipboard}>
                        <IconAddToCopy />
                        {t.GLOBAL.add_to_copy()}
                    </MessageAction>
                </Tooltip.Content>
            </Tooltip.Portal>
        </Tooltip.Root>
    )
}
