import type {FormEvent, SyntheticEvent} from 'react'
import React, {useRef} from 'react'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {isAfterCallWork} from '@bpinc/ad-interaction-state-methods'
import {useChatAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {CircleButton, FieldFrame, IconSendFilled} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {AttachmentsList, ContentEditable} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useChannelState} from '../../../channel-context'
import {useAgentTypingDetect} from './useAgentTypingDetect'
import {useAttachmentsList} from './useAttachmentsList'
import {useHasMultipleLines} from './useHasMultipleLines'


type ChatTextAreaProps = {
    currentMessage: string
    handleKeyDown(evt: SyntheticEvent): void
    handleChange(evt: FormEvent<HTMLElement>, value?: string): void
    sendCurrentMessage: () => void
    disabled?: boolean
    interactionStepId?: string
    itemId: string
    isExternalChat?: boolean
}

export const TEXT_AREA_DATA_ID = 'chat-input'

export const ChatTextArea: React.FC<ChatTextAreaProps> = React.memo(
    function TextArea({
        currentMessage,
        handleKeyDown,
        handleChange,
        disabled,
        isExternalChat,
        interactionStepId,
        sendCurrentMessage,
        itemId,
    }) {
        const isAttachmentsListReady = useChatAttachmentsUIState(
            'isAttachmentsListReady',
        )
        const chatItem = useInteraction(itemId)
        assertDefined(chatItem)

        const partyWithoutAddress =
            chatItem?.itemKind === 'potential'
                ? !chatItem.contactId && !chatItem?.displayData?.number
                : false

        const {t} = useLocalizationState()
        const handleBlur = useAgentTypingDetect([currentMessage], {
            interactionStepId,
            isExternalChat,
        })
        const {getSelectedChannel} = useChannelState()

        const isSmsChannel =
            getSelectedChannel(itemId)?.value.startsWith('sms')

        const attachmentsList = useAttachmentsList(itemId)

        const sendButtonDisabled =
            !isAttachmentsListReady(interactionStepId ?? '') ||
            isAfterCallWork(chatItem) ||
            partyWithoutAddress ||
            (!currentMessage.trim() && !attachmentsList.length)

        const removeAttachment = useChatAttachmentsUIState('removeAttachment')
        const contentEditableRef = useRef<HTMLDivElement>(null)
        const hasMultipleLines = useHasMultipleLines(contentEditableRef)
        assertDefined(interactionStepId)

        return (
            <FieldFrame
                disabled={disabled}
                contentClassName="p-0 overflow-hidden"
                className="max-h-36 flex-auto items-end overflow-hidden px-2 py-1.5"
                data-testid="chat-text-area"
            >
                <div className="flex flex-auto flex-col gap-1 overflow-hidden">
                    <AttachmentsList
                        className={tcn('flex flex-wrap gap-1 overflow-auto', {
                            'flex-nowrap': hasMultipleLines,
                        })}
                        list={attachmentsList}
                        removeAttachment={removeAttachment}
                        attachmentsListGroupId={interactionStepId}
                    />
                    <div className="flex cursor-text items-end overflow-hidden">
                        <ContentEditable
                            ref={contentEditableRef}
                            data-testid="chat-text-area-input"
                            onKeyDown={handleKeyDown}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            className="h-full w-full overflow-auto"
                            placeholder={t.INTERNAL_CHAT.message_input_placeholder()}
                            plainTextOnly={isSmsChannel}
                            data-id={TEXT_AREA_DATA_ID}
                            value={currentMessage}
                            disabled={disabled}
                        />
                        <CircleButton
                            className={tcn(
                                'size-6 state-layer-bg-primary-600',
                                'hover:state-layer-mix-dark-on-teal-1',
                                'focus-visible:state-layer-mix-dark-on-teal-2',
                                'active:state-layer-mix-dark-on-teal-3',
                                {
                                    'bg-primary-container-disabled':
                                        sendButtonDisabled,
                                },
                            )}
                            onClick={sendCurrentMessage}
                            tooltip={{
                                content:
                                    t.EXTERNAL_CHAT.send_message_btn_tooltip(),
                            }}
                            disabled={sendButtonDisabled}
                            //TODO: Add throttle
                            // clickThrottle={500}
                            data-testid="send-button"
                        >
                            <IconSendFilled
                                className={tcn('size-4 icon-reversed', {
                                    'icon-disabled': sendButtonDisabled,
                                })}
                            />
                        </CircleButton>
                    </div>
                </div>
            </FieldFrame>
        )
    },
)
