import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {useErrorValidation} from '@bpinc/ad-error-hooks'
import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {
    getAssociatedChatServiceId,
    getContactId,
    getMessengers,
    getPhoneFromItem,
    getServiceId,
} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useServiceState} from '@bpinc/ad-service-context'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {useEvent} from '@bpinc/lib-helpers'
import {useNotificationsState} from '@bpinc/lib-notifications-context'

import {useChannelState} from '../channel-context'


interface InitiateChatProps {
    chatItem: Item
}


//TODO: Extend this function to use it for text messages as well (error validation logic is slightly different)

/**
 * Initiates a chat with the selected channel and address.
 * @returns boolean. true if the chat was initiated, false otherwise.
 */
export function useInitiateChatByAttachment({
    chatItem,
}: InitiateChatProps): () => boolean {
    const {showNotification, hideNotificationsByTag} =
        useNotificationsState('methods')
    const contacts = useContactsState('contacts')
    const {getSelectedChannel, getSelectedPhone} = useChannelState()
    const selectedChannel = getSelectedChannel(chatItem.id)
    const selectedPhone = getSelectedPhone(chatItem.id)
    const contactMessengers = getMessengers(contacts, chatItem)
    const phone = selectedPhone?.phone || getPhoneFromItem(chatItem)
    const errorValidation = useErrorValidation()
    const {selectedService} = useServiceState()
    const {selectedDIDNumber} = useDIDNumberState()
    const contactId = getContactId(chatItem)
    const itemService = getServiceId(chatItem)
    const {initiateChat} = useInteractionStateMethods()
    const service = itemService || selectedService
    const {services} = useServicesDispositionsState()
    const associatedChatServiceId = getAssociatedChatServiceId(
        services,
        selectedService,
    )
    return useEvent(() => {
        /**
         * Get the messenger address based on the selected channel value or fallback to the phone number.
         */
        const messengerAddress = contactMessengers?.find(
            messenger => messenger.type === selectedChannel?.value,
        )?.userId

        const address = messengerAddress || phone

        /**
         * If there is no phone number or messenger available do nothing.
         */
        if (!address) {
            return false
        }

        /**
         * Check for any validation errors.
         */
        const errors = [
            ...errorValidation.getServiceAndDIDNumbersErrors({
                service,
                didNumber: selectedDIDNumber,
                interactionType: 'sms',
            }),
            ...errorValidation.getChannelError({
                selectedChannel: selectedChannel?.value,
            }),
        ]

        /**
         * If there are any errors, show the first one and return.
         */
        if (errors.length) {
            showNotification(errors[0])
            return false
        }

        /**
         * If there are no errors, hide existing 'interaction-init' errors and start the chat initiation process.
         */
        hideNotificationsByTag('interaction-init')

        initiateChat({
            channel: selectedChannel?.value,
            address,
            displayData: chatItem?.displayData,
            serviceId: associatedChatServiceId,
            accessNumberId: selectedDIDNumber,
            contactId,
            origination: 'interaction',
        })

        return true
    })
}
