import {formatDate} from '@bpinc/lib-helpers'
import {IconRobot} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'


interface WithTopInfoProps {
    displayName?: string
    isOutgoing?: boolean
    isDelivered?: boolean
    isRead?: boolean
    isSystemMessage?: boolean
    showTime?: boolean
    sendTime?: number
    showName?: boolean
}

export const WithTopInfo: React.FC<React.PropsWithChildren<WithTopInfoProps>> = props => {
    const {
        children,
        displayName,
        isOutgoing,
        isSystemMessage,
        sendTime,
        showTime,
        showName,
    } = props

    const {t} = useLocalizationState()

    const siblingClassName = tcn('text-neutral-fade [&:not(:last-child)]:mr-2')

    return (
        <>
            <div className="flex justify-between pb-1 text-sm leading-[16px]">
                <div className={tcn('flex items-center justify-start', {'w-full justify-end': isOutgoing})}>
                    {isSystemMessage && <IconRobot className="mr-2" />}
                    {(showName || isSystemMessage) && (
                        <span
                            className={siblingClassName}
                            data-testid="party-display-name"
                        >
                            {isSystemMessage ? t.EXTERNAL_CHAT.system_message() : displayName}
                        </span>
                    )}
                    {showTime && (
                        <span
                            className={siblingClassName}
                            data-testid="message-time"
                        >
                            {formatDate(sendTime, 'h:mm A')}
                        </span>
                    )}
                    {/* @TODO - implement isDelivered && isRead */}
                    {/* {isOutgoing && isDelivered && !isRead && <IconDelivered className="[&:not(:last-child)]:mr-2" />} */}
                    {/* {isOutgoing && isRead && <IconRead className="[&:not(:last-child)]:mr-2 [&_path]:fill-primary-600" />} */}
                </div>
            </div>
            {children}
        </>
    )
}
