import type {FC} from 'react'
import React, {useCallback, useState} from 'react'

import type {VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'
import {useMainPageUIState} from '@bpinc/ad-main-page-ui-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {OnDragEndParams} from '@bpinc/lib-ui-elements'
import {Floating, LinkLikeButton, useCanPin} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {BotSuggestions} from './BotSuggestions'
import {VoiceTranscriptKB} from './KnowledgeBase'


const FLOATING_PANEL_MIN_WIDTH = 400

const activeFloatingPanelTabStyles = tcn(`
        text-xl
        text-main
        focus:text-main 
        hover:focus:text-main
    `)

interface FloatingPanelProps {
    itemId: string
    botSuggestions: VoiceTranscriptSuggestion[] | undefined
}

export const RightPanel: FC<FloatingPanelProps> = ({itemId, botSuggestions}) => {
    const {t} = useLocalizationState()
    const [floatingPanelContent, setFloatingPanelContent] = useState<
      'kb' | 'bot'
    >('kb')

    const {
        kbFloatingState,
        kbWidth,
        mainContentNode,
        setKBFloatingState,
        setKBWidth,
    } = useMainPageUIState(['kbFloatingState', 'kbWidth', 'setKBFloatingState', 'setKBWidth', 'mainContentNode'])

    const canPin = useCanPin({
        mainContentNode,
        floatingState: kbFloatingState,
        floatingSize: kbWidth,
        contentMinWidth: 580,
    })

    const onDragEnd = useCallback(
        function onDragEndCallback({size}: OnDragEndParams) {
            setKBWidth(size)
        },
        [setKBWidth],
    )

    return (
        <Floating
            maxSizeInPercent={60}
            canPin={canPin}
            className="h-full"
            closeByEscape={kbFloatingState === 'floating'}
            data-testid="kb-panel"
            initialSize={kbWidth}
            minSize={FLOATING_PANEL_MIN_WIDTH}
            onDragEnd={onDragEnd}
            resizeable
            setState={setKBFloatingState}
            side="right"
            state={kbFloatingState}
        >
            <div className="flex h-full w-full flex-col">
                <header className="flex items-center gap-6 px-4 pt-5">
                    <LinkLikeButton
                        className={tcn({[activeFloatingPanelTabStyles]: floatingPanelContent === 'bot'})}
                        onClick={() => {
                            setFloatingPanelContent('bot')
                        }}
                    >
                        {t.AGENT_ASSIST.suggestions_tab()}
                    </LinkLikeButton>
                    <LinkLikeButton
                        className={tcn({[activeFloatingPanelTabStyles]: floatingPanelContent === 'kb'})}
                        onClick={() => {
                            setFloatingPanelContent('kb')
                        }}
                    >
                        {t.AGENT_ASSIST.kb_tab()}
                    </LinkLikeButton>
                </header>
                <section className="flex h-full flex-auto flex-col overflow-auto">
                    {floatingPanelContent === 'kb' && <VoiceTranscriptKB />}
                    {floatingPanelContent === 'bot' && (
                        <BotSuggestions
                            itemId={itemId}
                            botSuggestions={botSuggestions}
                        />
                    )}
                </section>
            </div>
        </Floating>
    )
}

