import type {ReactNode} from 'react'
import React from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import {isScenarioMessage} from '@bpinc/ad-interaction-state-methods'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {
    getExternalChatFileData,
    getExternalChatMessageDisplayName,
    getExternalChatMessageDisplayType,
    getMessageId,
    isErrorExternalChatMessage,
} from '../../../../helpers'
import type {ExternalChatMessage} from '../../../../types'
import {isActivityMessageGroup, isChatMessage, isHistoryCallMessage, isHistoryMessage} from '../../../../types'
import {WithHoverOverActions} from '../../ChatMessagesActions'
import {MessageAvatar} from './MessageAvatar'
import {MessageContent} from './MessageContent'
import {WithTopInfo} from './WithTopInfo'

import styles from '../../../../styles/Chat.module.less'


type WithOptionalNameAndAvatarProps = {
    className?: string
    message: ExternalChatMessage
    children: ReactNode
    isFullWidth?: boolean
    messageClassName?: string
    withoutBorder?: boolean
}

export const WithOptionalNameAndAvatar: React.FC<WithOptionalNameAndAvatarProps> = React.memo(
    function WithOptionalNameAndAvatar({className, children, message, isFullWidth, messageClassName, withoutBorder}) {
        const {formatFullName} = useFormatting()
        const {userId, isSelf, timestamp} = message
        const id = getMessageId(message)
        const displayType = getExternalChatMessageDisplayType(message)

        const isChat = isChatMessage(message)
        const isLink = isChat ? !!message.page : false
        const source = isChat ? message.source : undefined

        const isActivityGroup = isActivityMessageGroup(message)
        const isNotes = isActivityGroup ? !!message.notes : false
        const isDisposition = isActivityGroup ? !!message.disposition : false

        const isHistory = isHistoryMessage(message)
        const direction = isHistory ? message.direction : undefined
        const mediaType = isHistory ? message.mediaType : undefined
        const isEmail = mediaType === 'EMAIL' && !isActivityGroup
        const isCall = mediaType === 'VOICE' && !isActivityGroup

        const callUsers = isHistoryCallMessage(message) ? message.users : undefined

        const isError = isErrorExternalChatMessage(message)
        const isFile = !!getExternalChatFileData(message)

        const isTextMessage = !isActivityGroup && !isEmail && !isFile && !isCall
        const isRemoteAssistSession = mediaType === 'REMOTE_ASSIST'
        const isCallConference = callUsers && callUsers.length > 1

        const isOutgoing =
            direction === 'outgoing' ||
            source === 'agent' ||
            source === 'system' ||
            displayType === 'navigation' ||
            displayType === 'activityGroup' ||
            isError ||
            isDisposition ||
            isSelf && !isCall

        const isCustomMessage = displayType === 'activityGroup' || mediaType === 'REMOTE_ASSIST'

        const fullName = getExternalChatMessageDisplayName(message, formatFullName)

        const isSystemMessage = isOutgoing && (!isSelf && !message?.userId)

        const showName = Boolean(isSystemMessage || (isCall && !isOutgoing && !isSelf) || (!isOutgoing && fullName))

        const isScenario = isScenarioMessage(displayType)

        const finalDirection = isOutgoing ? 'outgoing' : 'incoming'

        const finalUserId = callUsers?.[0].userId ?? userId

        const messageItemClassName = tcn(
            styles.messageBlock,
            styles[finalDirection],
        )
        return (
            <div
                className={messageItemClassName}
                data-disposition={isDisposition || undefined}
                data-email={isEmail || undefined}
                data-error={isError || undefined}
                data-file={isFile || undefined}
                data-incoming={!isOutgoing || undefined}
                data-testid="message-item"
                data-link={isLink || undefined}
                data-notes={isNotes || undefined}
                data-outgoing={isOutgoing || undefined}
                data-remote-assist-session={isRemoteAssistSession || undefined}
                data-text-message={isTextMessage || undefined}
            >
                <div className={tcn('z-0 flex max-w-[71%] flex-auto flex-col', {'w-full max-w-full': isFullWidth})}>
                    <section className={tcn(styles.message, styles[finalDirection])}>
                        {finalDirection === 'incoming' && !withoutBorder && (
                            <MessageAvatar
                                userId={finalUserId}
                                isConference={isCallConference}
                                displayName={fullName}
                                isScenarioAvatar={isScenario}
                                showUserAvatar
                            />
                        )}
                        {isCustomMessage ? (
                            children
                        ) : (
                            <MessageContent
                                className={tcn('relative', {'w-full max-w-full': isFullWidth}, className)}
                                isError={isError}
                                messageId={id}
                            >
                                <WithHoverOverActions
                                    messageId={id}
                                    className={tcn(
                                        `relative inline-block
                                        whitespace-normal break-all rounded border p-2
                                        border-on-secondary text-primary-on-primary before:absolute
                                        before:-bottom-px before:z-[-1] before:h-0 before:w-0 before:rounded-b-[33%]
                                        before:border-b-4 before:border-t-0
                                        before:border-transparent before:border-b-secondary-100 before:content-[''] after:absolute
                                        after:bottom-0 after:z-0 after:h-0 after:w-0 after:rounded-b-[33%] after:border-b-[9px]
                                        after:border-t-0 after:border-transparent after:content-[''] hover:text-primary-on-primary`,
                                        {
                                            // we could use rounded-bl-md instead of rounded-bl-[8px],
                                            // however we couldn't, the layout goes break down
                                            [`rounded-bl-[8px] bg-secondary-50
                                            before:left-[-5px] before:border-l-[9px] before:border-r-0
                                            after:left-[-5px] after:border-l-[8px]
                                            after:border-r-0 after:border-b-secondary-50`]: finalDirection === 'incoming',
                                            [`rounded-br-[8px] bg-primary-background
                                            before:right-[-5px] before:border-l-0 before:border-r-[9px]
                                            after:right-[-5px] after:border-l-0
                                            after:border-r-[8px] after:border-b-neutral-0`]: finalDirection === 'outgoing',
                                        },
                                        messageClassName,
                                    )}
                                >
                                    {withoutBorder
                                        ? children
                                        : (
                                            <WithTopInfo
                                                displayName={fullName}
                                                isOutgoing={isOutgoing}
                                                isSystemMessage={isSystemMessage}
                                                sendTime={timestamp}
                                                showName={showName}
                                                showTime={!isCall || isActivityGroup}
                                            >
                                                {children}
                                            </WithTopInfo>
                                        )
                                    }
                                </WithHoverOverActions>
                            </MessageContent>
                        )}
                    </section>
                </div>
            </div>
        )
    },
)
