import {tcn} from '@bpinc/lib-tailwind-classnames'


const dotStyles = 'size-1.5 rounded bg-inverse-disabled inline-block'

export const DotsTransforming = () => {
    return (
        <>
            <div className={tcn(dotStyles, 'ml-1.5 mr-1 animate-[dot-transform_1s_alternate_infinite]')} />
            <div className={tcn(dotStyles, 'mr-1 animate-[dot-transform_1s_500ms_alternate_infinite]')} />
            <div className={tcn(dotStyles, 'animate-[dot-transform_1s_1.5s_alternate_infinite]')} />
        </>
    )
}
