import React from 'react'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {createComponentLoader} from '@bpinc/lib-ui-elements'
import type {GroupChatUIScope} from '@bpinc/state-group-chat-ui'

import {ExternalChat} from './external/ExternalChat'


const {ChatCenterInteractionLoader} = createComponentLoader(
    'chatCenter', () => import('chatCenter'), 'ChatCenterInteraction',
)

type Props = {
    itemId: string
    scope: GroupChatUIScope
}

export const Chat: React.FC<Props> = React.memo(function Chat({itemId, scope}) {
    const item = useInteraction(itemId)
    const isInternalChat = Boolean(item && (
        item.groupChatId ||
        item.groupChatMembers ||
        item.userId
    ))

    return isInternalChat
        ? <ChatCenterInteractionLoader scope={scope} interactionId={itemId} />
        : <ExternalChat itemId={itemId} />
})
