import {useCallback, useEffect, useMemo, useRef} from 'react'

import {
    useInteractionChatStateMethods,
    useInteractionChatStateSelector,
} from '@bpinc/ad-interaction-chat-state-context'
import type {MessageSource} from '@bpinc/ad-interaction-message-types'
import {useIntersectionObserver, usePageVisibility} from '@bpinc/lib-helpers'

import type {ChatScrollNodeType, HistoryMessage} from '../types'


interface UseMarkAsReadVisibilitySensor {
    msgId?: string | undefined
    scrollNode: ChatScrollNodeType
    mediaType?: HistoryMessage['mediaType']
    source?: MessageSource
    isRead?: boolean
    historyMessageGlobalInteractionId?: string

    isDIDChat: boolean
    itemGlobalInteractionId?: string
    itemInteractionStepId: string
    itemContactId: string
}

export function useMarkAsReadVisibilitySensor({
    msgId,
    scrollNode,
    mediaType,
    isDIDChat,
    itemInteractionStepId,
    itemContactId,
    itemGlobalInteractionId,
    source,
    isRead,
    historyMessageGlobalInteractionId,
}: UseMarkAsReadVisibilitySensor) {

    const pageIsVisible = usePageVisibility()

    const {markMessageAsRead, markPersonalMessageAsRead} = useInteractionChatStateMethods()

    const unreadActivityHistory = useInteractionChatStateSelector(
        s =>
            !!itemContactId &&
            !!msgId &&
            !!historyMessageGlobalInteractionId &&
            !!s.unreadPersonalMessages[itemContactId]?.[historyMessageGlobalInteractionId]?.[msgId],
    )

    const visibilitySensorIsActive = useMemo(() => {
        if (!pageIsVisible) {
            return false
        }

        if (isActivityHistoryMessage()) {
            return unreadActivityHistory
        }

        return itemIsActivated() && isNotServiceMessage() && !isRead

        function isActivityHistoryMessage() {
            return mediaType
        }
        function itemIsActivated() {
            return !!itemInteractionStepId
        }

        function isNotServiceMessage() {
            return source === 'customer' || source === 'agent'
        }
    }, [itemInteractionStepId, isRead, mediaType, pageIsVisible, source, unreadActivityHistory])

    const ref = useRef<HTMLDivElement>(null)

    const {isIntersecting} = useIntersectionObserver(ref, {
        root: scrollNode,
        enabled: visibilitySensorIsActive,
    })

    const markAsRead = useCallback(
        function handleVisibleCallback(visible: boolean) {
            if (visible) {
                if (unreadActivityHistory) {
                    if (historyMessageGlobalInteractionId) {
                        markPersonalMessageAsRead({
                            globalInteractionId: historyMessageGlobalInteractionId,
                            messageId: msgId!,
                            contactId: itemContactId,
                        })
                    }
                } else if (msgId) {
                    if (itemInteractionStepId) {
                        markMessageAsRead({
                            interactionStepId: itemInteractionStepId,
                            messageId: msgId,
                        })
                    }
                    if (itemGlobalInteractionId) {
                        if (isDIDChat && itemContactId) {
                            markPersonalMessageAsRead({
                                globalInteractionId: itemGlobalInteractionId,
                                messageId: msgId,
                                contactId: itemContactId,
                            })
                        }
                    }
                }
            }
        },
        [
            unreadActivityHistory,
            msgId,
            markPersonalMessageAsRead,
            historyMessageGlobalInteractionId,
            itemContactId,
            itemInteractionStepId,
            itemGlobalInteractionId,
            markMessageAsRead,
            isDIDChat,
        ],
    )

    useEffect(() => {
        if (isIntersecting && visibilitySensorIsActive) {
            markAsRead(true)
        }
    }, [markAsRead, isIntersecting, visibilitySensorIsActive])

    return ref
}
