import type React from 'react'
import type {FormEvent, KeyboardEvent} from 'react'
import {useCallback, useMemo} from 'react'
import produce from 'immer'
import {useAtom} from 'jotai'
import {get} from 'lodash-es'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useDidState} from '@bpinc/ad-did-context'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import {useErrorValidation} from '@bpinc/ad-error-hooks'
import {useInteractionChatStateMethods} from '@bpinc/ad-interaction-chat-state-context'
import type {MessengerType} from '@bpinc/ad-interaction-chat-state-types'
import {messengerTypes} from '@bpinc/ad-interaction-chat-state-types'
import {useInteraction, useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import {
    getAssociatedChatServiceId,
    getInteractionStepId,
    getMessengers,
    getPhoneFromItem,
    getServiceId,
    isCall,
    isExternalChat,
    isOutboundInteraction,
    isPotentialInteraction,
} from '@bpinc/ad-interaction-state-methods'
import {useIsOffline, useOfflineStateMethods} from '@bpinc/ad-offline-state-context'
import {useServiceState} from '@bpinc/ad-service-context'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import {useChatAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {useEvent} from '@bpinc/lib-helpers'
import {condenseNewLines, isHTMLElement} from '@bpinc/lib-html-utils'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {getCursorPosition, moveCursorToTheEnd, setCursorPosition} from '@bpinc/lib-ui-elements'
import {useInteractionUIState} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useChannelState} from '../../../channel-context'
import {useMessageUpdater} from '../../../hooks/useMessageUpdater'
import {chatPopupContentAtom, predefinedMessagesSearchAtom, predefinedSearchInsertPositionAtom} from './atoms'
import {TEXT_AREA_DATA_ID} from './ChatTextArea'


interface UseChatMessagesParams {
    itemId: string
    serviceId?: string
}

interface UseChatMessagesOutput {
    currentMessage: string
    setCurrentMessage: (message: React.SetStateAction<string>) => void

    sendCurrentMessage(): void

    handleKeyDown(event: KeyboardEvent<HTMLTextAreaElement>): void

    handleTextareaChange(event: FormEvent<HTMLElement>, value: string): void

    updateMessage<T, S extends string>(
        arg0?: (arg0: T) => S
    ): (arg0: T) => void

    handleSelectPredefinedMessage: (text: string) => void
}

export function useChatMessages(
    params: UseChatMessagesParams,
): UseChatMessagesOutput {
    const [chatPopupContent, setChatPopupContent] = useAtom(
        chatPopupContentAtom,
    )

    const {itemId, serviceId} = params

    const [predefinedSearchInsertPosition, setPredefinedSearchInsertPoisition] =
        useAtom(predefinedSearchInsertPositionAtom)
    const [predefinedMessagesSearch, setPredefinedMessagesSearch] = useAtom(
        predefinedMessagesSearchAtom,
    )
    const {showNotification, hideNotificationById, hideNotificationsByTag} =
        useNotificationsState('methods')
    const errorValidation = useErrorValidation()
    const {getSelectedChannel, getSelectedPhone} = useChannelState()
    const channel = getSelectedChannel(itemId)?.value
    const contacts = useContactsState('contacts')
    const isOffline = useIsOffline()
    const {setShowOfflineAlert} = useOfflineStateMethods()
    const {ERROR_MESSAGES: i18n} = useLocalizationState('t')

    const chatActiveItem = useInteraction(itemId)

    const id = chatActiveItem?.id ?? ''
    const contactMessengers = getMessengers(contacts, chatActiveItem)
    const interactionStepId = getInteractionStepId(chatActiveItem)

    const {initiateChat} = useInteractionStateMethods()
    const {sendMessage, addMessageToPending, sendFile, openChannel} =
        useInteractionChatStateMethods()

    const {getItemState, setItemState} = useInteractionUIState()

    const {clearAttachmentsList, getAttachmentsList, isAttachmentsListReady} =
        useChatAttachmentsUIState([
            'clearAttachmentsList',
            'getAttachmentsList',
            'isAttachmentsListReady',
        ])

    const {selectedService} = useServiceState()
    const didNumbers = useDidState('didNumbers')
    const {selectedDIDNumber} = useDIDNumberState()

    const associatedCallsNumber =
        isCall(chatActiveItem) && chatActiveItem?.callData?.incoming
            ? chatActiveItem.callData.fromNumber
            : chatActiveItem?.callData?.destination

    const phone = getSelectedPhone(itemId)?.phone || getPhoneFromItem(chatActiveItem)
    const sameNumberWithAssociatedCall = associatedCallsNumber === phone

    const didNumber = useMemo(
        function memoizeDidNumber() {
            if (isCall(chatActiveItem)) {
                if (isOutboundInteraction(chatActiveItem)) {
                    const callHasSmsDIDNumber =
                        chatActiveItem?.accessNumberId &&
                        didNumbers[chatActiveItem.accessNumberId]?.isSMS

                    if (sameNumberWithAssociatedCall && callHasSmsDIDNumber) {
                        return chatActiveItem!.accessNumberId
                    }
                } else {
                    const isDID = chatActiveItem?.scenarioData?.isDID
                    if (
                        isDID &&
                        (!selectedDIDNumber ||
                            didNumbers[selectedDIDNumber]?.isSMS)
                    ) {
                        for (const accessNumberId of Object.keys(didNumbers)) {
                            if (didNumbers[accessNumberId].isSMS) {
                                return accessNumberId
                            }
                        }
                    }
                }
            }

            return selectedDIDNumber
        },
        [
            chatActiveItem,
            didNumbers,
            sameNumberWithAssociatedCall,
            selectedDIDNumber,
        ],
    )

    const itemService = getServiceId(chatActiveItem)
    const service =
        serviceId ||
        (sameNumberWithAssociatedCall ? itemService : selectedService)
    const {services} = useServicesDispositionsState()
    const associatedChatService =
        getAssociatedChatServiceId(services, service) ||
        getAssociatedChatServiceId(services, selectedService)

    const currentMessage = getItemState(id)?.chatMessage ?? ''
    const attachmentsList = getAttachmentsList(interactionStepId)

    const setCurrentMessage = useCallback(
        function setCurrentMessageCallback(
            setMessage: React.SetStateAction<string>,
        ) {
            setItemState(
                id,
                produce(state => {
                    if (typeof setMessage === 'string') {
                        state.chatMessage = setMessage
                    } else {
                        state.chatMessage = setMessage(state.chatMessage)
                    }
                }),
            )
        },
        [id, setItemState],
    )

    const resetCurrentMessage = useCallback(
        function resetCurrentMessageCallback() {
            !isOffline && setCurrentMessage('')
        },
        [isOffline, setCurrentMessage],
    )

    const sendCurrentPotentialMessage = useCallback(
        function sendCurrentPotentialMessageCallback() {
            const contactId = get(chatActiveItem, 'contactId')


            const messengerAddress = contactMessengers?.find(
                messenger => messenger.type === channel,
            )?.userId
            const address = messengerAddress || phone

            if (!address || !channel) {
                return
            }

            addMessageToPending(
                {
                    content: currentMessage,
                    channel,
                    type: 'message',
                },
                address,
            )
            initiateChat({
                channel,
                address,
                serviceId: associatedChatService,
                accessNumberId: didNumber,
                contactId,
                displayData: chatActiveItem?.displayData,
                origination: 'interaction',
            })

            return resetCurrentMessage()
        },
        [
            channel,
            chatActiveItem,
            contactMessengers,
            phone,
            addMessageToPending,
            currentMessage,
            initiateChat,
            associatedChatService,
            didNumber,
            resetCurrentMessage,
        ],
    )

    const sendCurrentExternalMessage = useCallback(
        function sendCurrentExternalMessageCallback() {
            //if chat is initialized
            if (isExternalChat(chatActiveItem)) {
                if (isAttachmentsListReady(interactionStepId)) {
                    if (currentMessage) {
                        sendMessage({
                            content: condenseNewLines(currentMessage),
                            itemId: interactionStepId,
                            channel,
                            type: 'message',
                        })
                    }

                    if (attachmentsList.length) {
                        attachmentsList.forEach(attachment => {
                            if (attachment.fileData)
                                sendFile({
                                    fileData: attachment.fileData,
                                    itemId: interactionStepId,
                                    channel: channel || 'sms',
                                })
                        })
                        clearAttachmentsList(interactionStepId)
                    }
                    resetCurrentMessage()
                }
            } else {
                const messengerAddress = contactMessengers?.find(
                    messenger => messenger.type === channel,
                )?.userId
                const address = messengerAddress || phone

                if (
                    service &&
                    chatActiveItem &&
                    associatedChatService &&
                    channel &&
                    address
                ) {
                    addMessageToPending(
                        {
                            content: currentMessage,
                            channel,
                            type: 'message',
                        },
                        address,
                    )
                    initiateChat({
                        channel,
                        address,
                        serviceId: associatedChatService,
                        contactId: chatActiveItem.contactId,
                        displayData: chatActiveItem.displayData,
                        accessNumberId: didNumber,
                        origination: 'interaction',
                    })
                }
                resetCurrentMessage()
            }
        },
        [
            phone,
            contactMessengers,
            channel,
            chatActiveItem,
            isAttachmentsListReady,
            interactionStepId,
            currentMessage,
            attachmentsList,
            resetCurrentMessage,
            sendMessage,
            clearAttachmentsList,
            sendFile,
            service,
            associatedChatService,
            addMessageToPending,
            initiateChat,
            didNumber,
        ],
    )

    const tryToParseCommandsInCurrentMessage = useCallback(() => {
        if (currentMessage.startsWith('/invite')) {
            const words = currentMessage.split(' ')
            if (words.length !== 2) {
                showNotification({
                    id: Notifications.ChatChannelInviteBadFormatError,
                    type: 'error',
                    message: i18n.chat_channel_invite_bad_format(),
                    tags: ['current-view'],
                    duration: 30_000,
                    closable: true,
                })
            } else {
                const inviteChannel = words[1] as MessengerType
                if (!messengerTypes.includes(inviteChannel)) {
                    showNotification({
                        id: Notifications.ChatChannelInviteUnknownMessengerError,
                        type: 'error',
                        tags: ['current-view'],
                        message: i18n.chat_channel_invite_unknown_messenger(),
                        duration: 30_000,
                        closable: true,
                    })
                } else {
                    hideNotificationById(
                        Notifications.ChatChannelInviteBadFormatError,
                    )
                    hideNotificationById(
                        Notifications.ChatChannelInviteUnknownMessengerError,
                    )

                    const messengerAddress = contactMessengers?.find(
                        messenger => messenger.type === inviteChannel,
                    )?.userId
                    const address = messengerAddress || phone

                    if (itemService && address) {
                        openChannel({
                            channel: inviteChannel,
                            address,
                            interactionStepId,
                            serviceId: itemService,
                        })
                    }
                    resetCurrentMessage()
                }
            }
            return true
        }
    }, [
        contactMessengers,
        currentMessage,
        hideNotificationById,
        interactionStepId,
        itemService,
        openChannel,
        phone,
        showNotification,
        resetCurrentMessage,
        i18n,
    ])

    const sendCurrentMessage = useCallback(
        function sendCurrentMessageCallback() {
            if (isOffline) {
                setShowOfflineAlert(true)
                return
            }

            if (currentMessage) {
                const scrollerApi = getItemState(itemId)?.scrollerApi
                scrollerApi?.scrollToLast()
            }

            const isPotentialChat =
                isPotentialInteraction(chatActiveItem) ||
                isCall(chatActiveItem)


            if (isPotentialChat) {
                const errors = [
                    ...errorValidation.getServiceAndDIDNumbersErrors({
                        service,
                        didNumber,
                        interactionType: 'sms',
                    }),
                    ...errorValidation.getChannelError({
                        selectedChannel: channel,
                    }),
                ]

                if (errors.length) {
                    showNotification(errors[0])
                    return
                }
                if (currentMessage) {
                    const messageErrors = errorValidation.getMessageErrors({
                        message: currentMessage,
                        channel: channel || 'sms',
                    })
                    if (messageErrors.length) {
                        showNotification(messageErrors[0])
                        return
                    }
                }
                hideNotificationsByTag('interaction-init')

                return currentMessage && sendCurrentPotentialMessage()
            }
            const messageErrors = errorValidation.getMessageErrors({
                message: currentMessage,
                channel: channel || 'sms',
            })
            if (messageErrors.length) {
                showNotification(messageErrors[0])
                return
            }

            if (interactionStepId) {
                if (tryToParseCommandsInCurrentMessage()) {
                    return
                }
                return (
                    (currentMessage || attachmentsList.length) &&
                    sendCurrentExternalMessage()
                )
            }
        },
        [
            isOffline,
            currentMessage,
            chatActiveItem,
            channel,
            errorValidation,
            interactionStepId,
            setShowOfflineAlert,
            getItemState,
            itemId,
            service,
            didNumber,
            hideNotificationsByTag,
            sendCurrentPotentialMessage,
            showNotification,
            attachmentsList.length,
            sendCurrentExternalMessage,
            tryToParseCommandsInCurrentMessage,
        ],
    )

    const handleSelectPredefinedMessage = useEvent((text: string) => {
        setCurrentMessage(
            m =>
                m.slice(0, predefinedSearchInsertPosition) +
                text +
                m.slice(
                    predefinedSearchInsertPosition + 1,
                    predefinedMessagesSearch.length,
                ),
        )
        setPredefinedMessagesSearch('')
        setChatPopupContent(undefined)

        const htmlElement = document.querySelector(
            '[data-testid="chat-text-area-input"]',
        )

        if (!(htmlElement instanceof HTMLElement)) {
            return
        }

        setTimeout(() => {
            htmlElement.focus()
            if (predefinedSearchInsertPosition === -1) {
                moveCursorToTheEnd(htmlElement)
            } else {
                setCursorPosition(
                    htmlElement,
                    predefinedSearchInsertPosition + text.length,
                )
            }
        })
    })

    const handleKeyDown = useCallback(
        (event: KeyboardEvent<HTMLTextAreaElement>) => {
            const textareaElement =
                event.target instanceof HTMLElement ? event.target : undefined

            function exitPredefinedMessages() {
                setChatPopupContent(undefined)
                setPredefinedSearchInsertPoisition(-1)
                setPredefinedMessagesSearch('')
            }

            if (!textareaElement) {
                return
            }

            const cursorPosition = getCursorPosition(textareaElement)

            if (event.key === ';') {
                setPredefinedSearchInsertPoisition(cursorPosition)
                setChatPopupContent('predefinedMessages')
            }

            if (
                event.key === ' ' &&
                cursorPosition - 1 === predefinedSearchInsertPosition
            ) {
                exitPredefinedMessages()
            }

            if (event.key === 'Escape' && chatPopupContent === 'predefinedMessages') {
                exitPredefinedMessages()
                event.preventDefault()
                event.stopPropagation()
            }

            if (event.key === 'Enter') {
                if (event.shiftKey || event.ctrlKey) {
                    return
                }
                event.preventDefault()
                event.stopPropagation()
                sendCurrentMessage()
            }
        },
        [
            chatPopupContent,
            setChatPopupContent,
            predefinedSearchInsertPosition,
            sendCurrentMessage,
            setPredefinedMessagesSearch,
            setPredefinedSearchInsertPoisition,
        ],
    )

    const handleTextareaChange = useCallback(
        function handleTextareaChangeCallback(
            event: FormEvent<HTMLElement>,
            value: string,
        ) {
            const el = event.target
            if (!isHTMLElement(el)) {
                return
            }
            el.scrollTop = el.scrollHeight

            const cursorPosition = getCursorPosition(el)

            setCurrentMessage(value)
            if (
                predefinedMessagesSearch &&
                cursorPosition <= predefinedSearchInsertPosition
            ) {
                setChatPopupContent(undefined)
                setPredefinedMessagesSearch('')
            } else {
                setPredefinedMessagesSearch(
                    chatPopupContent === 'predefinedMessages'
                        ? value.slice(predefinedSearchInsertPosition + 1)
                        : '',
                )
            }
        },
        [chatPopupContent, predefinedMessagesSearch, predefinedSearchInsertPosition, setChatPopupContent, setCurrentMessage, setPredefinedMessagesSearch],
    )

    const updateMessage = useMessageUpdater(
        setCurrentMessage,
        `[data-id="${TEXT_AREA_DATA_ID}"]`,
    )

    return useMemo(
        function memoizeOutput() {
            return {
                handleSelectPredefinedMessage,
                sendCurrentMessage,
                handleKeyDown,
                currentMessage,
                setCurrentMessage,
                handleTextareaChange,
                setPredefinedMessagesSearch,
                predefinedMessagesSearch,
                updateMessage,
            }
        },
        [
            handleSelectPredefinedMessage,
            predefinedMessagesSearch,
            setPredefinedMessagesSearch,
            currentMessage,
            setCurrentMessage,
            handleTextareaChange,
            sendCurrentMessage,
            handleKeyDown,
            updateMessage,
        ],
    )
}
