import {useEffect, useMemo} from 'react'
import {flow, partial, property} from 'lodash-es'

import {useContactsState, useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {useDIDNumberState} from '@bpinc/ad-did-number-context'
import type {ExternalChatChannelType} from '@bpinc/ad-interaction-chat-state-types'
import {useAssociatedChatInteraction} from '@bpinc/ad-interaction-state-context'
import {
    getChannel,
    getGlobalInteractionId,
    getMessengers,
    getPhoneFromItem,
    getServiceId,
    isCall,
    isChatService,
} from '@bpinc/ad-interaction-state-methods'
import {useRemoteAssistState} from '@bpinc/ad-remote-assist-state-context'
import {useServiceState} from '@bpinc/ad-service-context'
import {useServicesDispositionsState} from '@bpinc/ad-services-dispositions-context'
import type {OutboundChatChannel} from '@bpinc/ad-services-dispositions-types'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'
import type {LocalizationStateData} from '@bpinc/state-localization'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ChannelOption} from './types'


function mapChannel(
    t: LocalizationStateData['t'],
    channel: ExternalChatChannelType | undefined,
): ChannelOption | undefined {
    switch (channel) {
        case 'sms':
            return {value: channel, text: t.GLOBAL.chat_type_sms()}
        case 'web':
            return {value: channel, text: t.GLOBAL.chat_type_web()}
        case 'telegram':
            return {value: channel, text: t.GLOBAL.chat_type_telegram()}
        case 'line':
            return {value: channel, text: t.GLOBAL.chat_type_line()}
        case 'twitter':
            return {value: channel, text: t.GLOBAL.chat_type_twitter()}
        case 'facebook':
            return {value: channel, text: t.GLOBAL.chat_type_facebook()}
        case 'wechat':
            return {value: channel, text: t.GLOBAL.chat_type_wechat()}
        case 'nexmo':
            return {value: channel, text: t.GLOBAL.chat_type_nexmo()}
        case 'viber':
            return {value: channel, text: t.GLOBAL.chat_type_viber()}
        case 'whatsapp':
            return {value: channel, text: t.GLOBAL.chat_type_whatsapp()}
        case 'remoteassist':
            return {value: channel, text: t.GLOBAL.chat_type_remote_assist()}
    }
}

export function useOptions(): ChannelOption[] {
    const {getContact} = useContactsStateMethods()
    const contacts = useContactsState('contacts')
    const interactionId = useNonAssertedInteractionId()
    const chatActiveItem = useAssociatedChatInteraction(interactionId)
    const {services, allOutboundChatChannels, outboundChatChannels} = useServicesDispositionsState()
    const {selectedDIDNumber} = useDIDNumberState()
    const {selectedService} = useServiceState()
    const {items: raItems} = useRemoteAssistState()
    const {t} = useLocalizationState()

    const itemChannel = getChannel(chatActiveItem)
    const itemService = getServiceId(chatActiveItem)
    const messengers = getMessengers(contacts, chatActiveItem)
    const contactId = chatActiveItem?.contactId
    const originalChannel = chatActiveItem?.externalChatData?.channel
    const globalInteractionId = getGlobalInteractionId(chatActiveItem)
    const raSessionActive = globalInteractionId && raItems[globalInteractionId]?.clientState === 'joined'

    const isLoading = contactId && contacts[contactId]?.isLoading
    const contactExists = !!contactId && !!contacts[contactId]?.data

    useEffect(() => {
        if (contactId && !isLoading && !contactExists) {
            getContact({id: contactId})
        }
    }, [contactExists, contactId, getContact, isLoading])

    const localizedChannelOptions = useMemo(() => {
        const options: {[value: string]: ChannelOption} = {}

        addMessengersOptions()
        addRemoteAssistOption()
        addPhoneOptions()
        addOriginalChannelOption()
        filterOutboundOptions()
        addCurrentItemOption()

        return Object.values(options)

        function addOption(option: ChannelOption | undefined) {
            if (option) {
                options[option.value] = option
            }
        }

        function addMessengersOptions() {
            messengers?.forEach(flow(property('type'), partial(mapChannel, t), addOption))
        }

        function addPhoneOptions() {
            if (getPhoneFromItem(chatActiveItem)) {
                addOption({value: 'sms', text: t.GLOBAL.chat_type_sms(), phones: chatActiveItem?.displayData.phones})
            }
        }

        function filterOutboundOptions() {
            for (const key in options) {
                if (!outboundChannelAvailable(options[key])) {
                    delete options[key]
                }
            }

            function outboundChannelAvailable(option: ChannelOption) {
                const service = itemService || selectedService

                if (option.value === 'remoteassist') {
                    return raSessionActive
                }
                if (option.value === 'web') {
                    return true
                }
                if (option.value === originalChannel) {
                    return true
                }

                if (isCall(chatActiveItem)) {
                    return true
                }

                if (service) {
                    const outboundChannels = isChatService(service, services)
                        ? allOutboundChatChannels
                        : outboundChatChannels[service]

                    return outboundChannels?.includes(
                        option.value.toUpperCase() as OutboundChatChannel,
                    )
                }

                if (selectedDIDNumber) {
                    return option.value === 'sms'
                }
            }
        }

        function addCurrentItemOption() {
            const itemChannelOption = mapChannel(t, itemChannel)

            if (itemChannelOption) {
                addOption(itemChannelOption)
            }
        }

        function addOriginalChannelOption() {
            if (originalChannel) {
                const itemChannelOption = mapChannel(t, originalChannel)
                if (itemChannelOption) {
                    addOption(itemChannelOption)
                }
            }
        }

        function addRemoteAssistOption() {
            addOption({value: 'remoteassist', text: t.GLOBAL.chat_type_remote_assist()})
        }
    }, [
        messengers,
        t,
        chatActiveItem,
        itemService,
        selectedService,
        selectedDIDNumber,
        services,
        allOutboundChatChannels,
        outboundChatChannels,
        itemChannel,
        raSessionActive,
        originalChannel,
    ])


    return useMemo(
        function memoizedChannelOptions() {
            return localizedChannelOptions
        },
        [localizedChannelOptions],
    )
}
