import React from 'react'

import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'

import {NoMessages} from './NoMessages'


type NoMessagesPlaceholderProps = {
    contactId: string
    type: ChatType
}

export const NoMessagesPlaceholder: React.FC<NoMessagesPlaceholderProps> = ({contactId, type}) => {

    if (type !== 'messages' && type !== 'calls') {
        return null
    }

    return <NoMessages type={type} contactId={contactId} />
}
