import React, {useCallback, useEffect, useState} from 'react'
import type {PrimitiveAtom} from 'jotai'
import {useSetAtom} from 'jotai'
import {partial} from 'lodash-es'

import {getInteractionStepId, isCall, isPotentialInteraction} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useChatAttachmentsUIState} from '@bpinc/lib-attachments-state'
import {not, useDidUnmount} from '@bpinc/lib-helpers'
import {truthy} from '@bpinc/lib-types-helpers'
import {type InjectedFileInputProps, withFileInput} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {getFileRejection} from '../../../helpers'
import {useInitiateChatByAttachment} from '../../../hooks/useInitiateChatByAttachment'
import {UploadErrorModal} from './UploadErrorModal'


interface InvisibleAttachButtonProps {
    disabled?: boolean
    chatItem: Item
    buttonElementAtom: PrimitiveAtom<HTMLButtonElement | null>
}

/** It's invisible, and uncontrolled like every other file input element.
 * To control it, you need to pass the buttonElementAtom throuth the props
 * Ane later you can use the buttonElementAtom to trigger the click event like this:
 * const buttonElement = useAtomValue(buttonElementAtom)
 * buttonElement?.click()
 */
export const InvisibleAttachButton: React.FC<InvisibleAttachButtonProps> = ({
    disabled = false,
    chatItem,
    buttonElementAtom,
}) => {
    const setAttachmentButtonElement = useSetAtom(buttonElementAtom)

    const {
        addAttachment,
        fileRejections,
        setFileRejections,
        isAttachmentsListReady,
        getAttachmentsList,
    } = useChatAttachmentsUIState([
        'addAttachment',
        'fileRejections',
        'setFileRejections',
        'isAttachmentsListReady',
        'getAttachmentsList',
    ])
    const shouldInitiateChat =
        isPotentialInteraction(chatItem) || isCall(chatItem)
    const interactionStepId = getInteractionStepId(chatItem)

    const {t} = useLocalizationState()
    const openUploadErrorModal = useModalComponentOpener(UploadErrorModal)
    const handleUploadFiles = useCallback(
        (event: React.ChangeEvent<HTMLInputElement>): void => {
            addFilesToAttachmentsList()

            function addFilesToAttachmentsList() {
                const files = getFilesFromEvent()
                if (!files) {
                    return
                }

                const getLocalizedFileError = partial(getFileRejection, t)
                const fileRejection = truthy(files.map(getLocalizedFileError))
                const validFiles = files.filter(not(getLocalizedFileError))

                if (fileRejection.length) {
                    openUploadErrorModal({
                        rejection: fileRejection,
                        totalFilesAmount: files.length,
                    })
                }

                if (interactionStepId) {
                    validFiles.forEach(
                        partial(addAttachment, interactionStepId),
                    )
                }
            }

            function getFilesFromEvent() {
                const input = event.target
                return input.files ? Array.from(input.files) : []
            }
        },
        [addAttachment, interactionStepId, openUploadErrorModal, t],
    )

    useEffect(
        function showRejections() {
            if (isAttachmentsListReady(interactionStepId) && fileRejections.length) {
                openUploadErrorModal({
                    rejection: fileRejections,
                    onCloseCallback: () => setFileRejections([]),
                    totalFilesAmount:
                        getAttachmentsList(interactionStepId).length +
                        fileRejections.length,
                })
            }
        },
        [
            fileRejections,
            getAttachmentsList,
            interactionStepId,
            isAttachmentsListReady,
            openUploadErrorModal,
            setFileRejections,
        ],
    )

    useDidUnmount(() => {
        setAttachmentButtonElement(null)
        setFileRejections([])
    })

    const initiateChat = useInitiateChatByAttachment({chatItem})
    /**
     * Callback function for clicking on a file input element.
     *
     * @remarks
     * Opens the file input dialog and initiates a chat if necessary.
     */
    const onFileClick = useCallback(async () => {
        /**
         * If the user is not supposed to initiate a chat, simply open the file input dialog.
         */
        if (!shouldInitiateChat) {
            return {selectFiles: true}
        }

        const chatWasInitiated = initiateChat()

        return {selectFiles: chatWasInitiated}
    }, [initiateChat, shouldInitiateChat])

    //This is workaround because passing atom value directly leads to infinite loop
    const [innerButtonElement, setInnerButtonElement] =
        useState<HTMLButtonElement | null>(null)

    useEffect(() => {
        setAttachmentButtonElement(innerButtonElement)
    }, [innerButtonElement, setAttachmentButtonElement])

    return (
        <InvisibleAttachButtonWithInput
            ref={element => {
                if (element instanceof HTMLButtonElement || element === null) {
                    setInnerButtonElement(element)
                }
            }}
            onFileInputClick={onFileClick}
            handleUploadFiles={handleUploadFiles}
            isDisabled={disabled}
        />
    )
}

const InvisinbleAttachButtonUI = React.forwardRef<
    HTMLButtonElement,
    InjectedFileInputProps
>(({onFileInputClick}, ref) => (
    <button
        onClick={onFileInputClick}
        ref={ref}
        className="invisible absolute z-[-1] w-0"
    />
))

const InvisibleAttachButtonWithInput = withFileInput(InvisinbleAttachButtonUI)
