import type {MutableRefObject, RefObject} from 'react'
import {useLayoutEffect, useMemo} from 'react'
import type {IndexLocationWithAlign, ListRange} from 'react-virtuoso'
import produce from 'immer'

import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'
import {useAsRef} from '@bpinc/lib-helpers'
import {useInteractionUIChatTypeTopItemId, useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'

import {findFirstVisibleItemWithOffset, getMessageId, isScrollAtBottom} from '../../../helpers'
import {useFirstUnreadMessage} from '../../../hooks/useFirstUnreadMessage'
import type {ChatCommonMessagesType, ChatScrollNodeType} from '../../../types'


export function useInitialScrollPosition(
    itemId: string,
    type: ChatType,
    messagesRef: MutableRefObject<ChatCommonMessagesType[]>,
    rangeRef: RefObject<ListRange | undefined>,
    scrollNode: ChatScrollNodeType,
) {
    const setInteractionUIState = useInteractionUIItemStateSetter()
    const firstUnreadMessageIdRef = useAsRef(useFirstUnreadMessage(itemId))
    const topItemRef = useAsRef(useInteractionUIChatTypeTopItemId(itemId, type))

    useLayoutEffect(() => {
        //Save current scroll position on unmount
        return () => {
            if (!rangeRef.current) {
                return
            }

            if (isScrollAtBottom(scrollNode)) {
                setInteractionUIState(itemId, produce(state => {
                    delete state.tabTopItemId[type]
                }))
                return
            }

            const {id, offset} = findFirstVisibleItemWithOffset(scrollNode, rangeRef.current.startIndex, messagesRef.current)
            setInteractionUIState(itemId, produce(state => {
                state.tabTopItemId[type] = {id, offset}
            }))
        }

    }, [itemId, messagesRef, rangeRef, scrollNode, setInteractionUIState, type])

    return useMemo<IndexLocationWithAlign>(() => {
        if (firstUnreadMessageIdRef.current) {
            const index = messagesRef.current.findIndex(m => getMessageId(m) === firstUnreadMessageIdRef.current)
            return {index, align: 'end'}
        }

        if (topItemRef.current) {
            const index = messagesRef.current.findIndex(m => getMessageId(m) === topItemRef.current?.id)
            return {index, align: 'start', offset: (topItemRef.current?.offset || 0)}
        }

        return {index: 'LAST'}
    }, [firstUnreadMessageIdRef, messagesRef, topItemRef])
}

