import React from 'react'
import * as Popover from '@radix-ui/react-popover'
import {useAtom, useAtomValue} from 'jotai'

import {useAssociatedChatInteraction, useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId, isExternalChat} from '@bpinc/ad-interaction-state-methods'
import {useCommunicatorUIState} from '@bpinc/lib-communicator-ui-context'
import {useBoolean, useEscapeManager, useResizeObserver} from '@bpinc/lib-helpers'
import {useChatResponsesVariablesParser} from '@bpinc/lib-helpers-ui'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'
import {CircleButton, IconPlus24, TriggerDropdown} from '@bpinc/lib-jskit-ui'
import {ViewPredefinedMessages} from '@bpinc/lib-predefined-messages-ui'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {EmojiPopup} from '@bpinc/lib-ui-elements'

import type {ChatProps} from '../../../types'
import {ChannelSelector} from '../ChannelSelector'
import {attachmentButtonElementAtom, chatPopupContentAtom, predefinedMessagesSearchAtom} from './atoms'
import {ChatTextArea} from './ChatTextArea'
import {InvisibleAttachButton} from './InvisibleAttachButton'
import {useChatMessages} from './useChatMessages'
import {useControlsOptions} from './useControlsOptions'
import {useGreetings} from './useGreeting'
import {useSmsMessageCounter} from './useSmsMessageCounter'

import styles from './ChatForm.module.less'


const PREDEFINED_MESSAGES_MAX_OFFSET = 24
const PREDEFINED_MESSAGES_SIDE_OFFSET = 5

function getChatMessagesElement() {
    const element = document.querySelector('[data-testid="chat-content"]')
    if (element instanceof HTMLElement) {
        return element
    }
}

export const ChatForm: React.FC<ChatProps> = React.memo(function ExternalChat({
    itemId,
    serviceId,
}) {
    const chatFormRef = useCommunicatorUIState('interactionChatFormRef')
    const activeInteractionId = useNonAssertedInteractionId()
    const {
        updateMessage,
        handleSelectPredefinedMessage,
        sendCurrentMessage,
        handleKeyDown,
        handleTextareaChange,
        setCurrentMessage,
        currentMessage,
    } = useChatMessages({itemId, serviceId})

    const predefinedMessagesSearch = useAtomValue(predefinedMessagesSearchAtom)
    const parseVariables = useChatResponsesVariablesParser()
    useGreetings({currentMessage, setCurrentMessage, itemId, parseVariables})

    const interactionStepId = useInteractionProp(itemId, getInteractionStepId)
    const itemIsExternalChat = useInteractionProp(itemId, isExternalChat)

    const {text: smsMessageCounter, enabled: smsMessageCounterEnabled} =
        useSmsMessageCounter(itemId, currentMessage)


    const messagesElementEntry = useResizeObserver(getChatMessagesElement())
    const [chatPopupContent, setChatPopupContent] =
    useAtom(chatPopupContentAtom)

    const chatItem = useAssociatedChatInteraction(itemId)
    assertDefined(chatItem)

    const controlsOptions = useControlsOptions({setCurrentMessage, itemId})
    const controlsDropdownOpen = useBoolean()
    useEscapeManager(() => {
        controlsDropdownOpen.setFalse()
    }, {enabled: controlsDropdownOpen.value})

    useEscapeManager(
        () => {
            setChatPopupContent(undefined)
        },
        {enabled: Boolean(chatPopupContent)},
    )


    return (
        <div
            ref={chatFormRef}
            className="flex px-4 py-2.5"
            data-testid="chat-bottom-panel"
        >
            <InvisibleAttachButton chatItem={chatItem} buttonElementAtom={attachmentButtonElementAtom} />
            {/* TODO Use it appropriately once design is added: */}
            {smsMessageCounterEnabled && (
                <span className={styles.smsMessageCounter}>
                    {smsMessageCounter}
                </span>
            )}
            <div className="flex flex-auto items-end gap-2">
                <div className="flex h-[33px] items-center gap-2">
                    {!!activeInteractionId && <ChannelSelector />}
                    <TriggerDropdown
                        data-chat-actions-menu
                        modal={false}
                        open={controlsDropdownOpen.value}
                        onOpenChange={controlsDropdownOpen.toggle}
                        className="group z-10"
                        align="start"
                        side="top"
                        options={controlsOptions.map(
                            ({Icon, text, shortcut, onClick}) => (
                                <button
                                    onClick={onClick}
                                    key={text}
                                    className="flex w-full min-w-[210px] items-center gap-2"
                                >
                                    <Icon className="size-4" />
                                    <p className="flex-auto caption-s text-primary-on-primary">
                                        {text}
                                    </p>
                                    <p className="caption-s text-neutral-disabled">
                                        {shortcut}
                                    </p>
                                </button>
                            ),
                        )}
                    >
                        <CircleButton>
                            <IconPlus24 className="size-4.5" />
                        </CircleButton>
                    </TriggerDropdown>
                </div>
                <Popover.Root
                    open={Boolean(chatPopupContent)}
                    onOpenChange={open => {
                        if (!open) {
                            setChatPopupContent(undefined)
                        }
                    }}
                >
                    <Popover.Trigger
                        asChild
                        className="flex flex-auto"
                        onClick={e => {
                            e.preventDefault()
                        }}
                    >
                        <div>
                            <ChatTextArea
                                itemId={itemId}
                                sendCurrentMessage={sendCurrentMessage}
                                interactionStepId={interactionStepId}
                                isExternalChat={itemIsExternalChat}
                                currentMessage={currentMessage}
                                handleKeyDown={handleKeyDown}
                                handleChange={handleTextareaChange}
                            />
                        </div>
                    </Popover.Trigger>
                    <Popover.Content
                        side="top"
                        align="start"
                        className="z-[100] w-[--radix-popover-trigger-width]"
                        sideOffset={PREDEFINED_MESSAGES_SIDE_OFFSET}
                        onOpenAutoFocus={e => {
                            e.preventDefault()
                        }}
                    >
                        {chatPopupContent === 'emojiPicker' ? (
                            <EmojiPopup
                                onSelectEmoji={emojiChar => {
                                    updateMessage((emoji: string) => emoji)(emojiChar)
                                    setChatPopupContent(undefined)
                                }}
                            />
                        ) : chatPopupContent === 'predefinedMessages' ? (
                            <ViewPredefinedMessages
                                handleSelect={handleSelectPredefinedMessage}
                                search={predefinedMessagesSearch}
                                className="w-[--radix-popover-trigger-width]"
                                style={{
                                    maxHeight:
                                        (messagesElementEntry?.contentRect
                                            .height ?? 0) -
                                        (PREDEFINED_MESSAGES_MAX_OFFSET -
                                            PREDEFINED_MESSAGES_SIDE_OFFSET),
                                }}
                            />
                        ) : null}
                    </Popover.Content>
                </Popover.Root>
            </div>
        </div>
    )
})
