import type {FC, HTMLAttributes} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


interface MessageActionProps extends HTMLAttributes<HTMLButtonElement> {
    children: React.ReactNode
}

export const MessageAction: FC<MessageActionProps> = ({
    className,
    children,
    ...buttonProps
}) => {
    return (
        <button
            {...buttonProps}
            className={tcn(
                'flex items-center gap-1 rounded border px-2 py-0.5 shadow-4 state-layer-bg-neutral-0 svg-color-current border-default',
                'transition-colors',
                'hover:state-layer-mix-dark-on-white-1',
                'focus:state-layer-mix-dark-on-white-2',
                'active:state-layer-mix-dark-on-white-3',
                className,
            )}
        >
            {children}
        </button>
    )
}
