import type {FC} from 'react'
import React from 'react'
import {Button} from '@servicepattern/ui'
import {useAtomValue} from 'jotai'
import {uniq} from 'lodash-es'

import {mimeTypeToExternalChatFileType} from '@bpinc/ad-formatting-helpers'
import {Attachment as AttachmentUI, InlineAlert, Modal} from '@bpinc/lib-jskit-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import type {getFileRejection} from '../../../helpers'
import {attachmentButtonElementAtom} from './atoms'


type UploadErrorModalProps = CommonModalProps & {
    rejection: Exclude<ReturnType<typeof getFileRejection>, null>[]
    totalFilesAmount: number
    onCloseCallback?: () => void
}

export const UploadErrorModal: FC<UploadErrorModalProps> = ({
    rejection,
    onClose,
    onCloseCallback,
    totalFilesAmount,
    ...props
}) => {
    const attachmentButton = useAtomValue(attachmentButtonElementAtom)
    const t = useLocalizationState('t')
    const errorMessages = uniq(rejection.flatMap(r => r.errors.map(e => e.message)))

    function handleClose() {
        onClose()
        onCloseCallback?.()
    }

    return (
        <Modal {...props} onClose={handleClose}>
            <Modal.Header type="error">
                {t.CHAT_CENTER.upload_failed()}
            </Modal.Header>
            <Modal.Content>
                <InlineAlert variant="error">
                    <ul className="m-0 p-0">
                        {errorMessages.map((message, index) => (
                            <li key={index}>{message}</li>
                        ))}
                    </ul>
                </InlineAlert>
                <p className="my-6">
                    {t.CHAT_CENTER.failed_to_upload(
                        rejection.length,
                        totalFilesAmount,
                    )}
                </p>
                <div className="flex flex-col gap-2">
                    {rejection.map((err, index) =>
                        mimeTypeToExternalChatFileType(err.file.type) ===
                    'attachment' ? (
                                <AttachmentUI.File
                                    key={index}
                                    name={err.file.name}
                                    className="pointer-events-none"
                                    isFailed
                                />
                            ) : (
                                <AttachmentUI.Image
                                    key={index}
                                    name={err.file.name}
                                    className="pointer-events-none"
                                    isFailed
                                />
                            ),
                    )}
                </div>
            </Modal.Content>
            <Modal.Actions>
                <Button
                    variant="secondary"
                    onClick={() => {
                        attachmentButton?.click()
                        handleClose()
                    }}
                >
                    {t.CHAT_CENTER.select_files()}
                </Button>
                <Button variant="primary" onClick={() => handleClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </Button>
            </Modal.Actions>
        </Modal>
    )
}
