import React from 'react'

import {isHTMLSuggestion, isLinkSuggestion} from '@bpinc/ad-interaction-state-types'

import type {MessageProps} from '../../VoiceTranscript/messages/Message'
import {Message} from '../../VoiceTranscript/messages/Message'
import {parseTranscriptLinksAsHTML} from '../../VoiceTranscript/utils'


type BotSuggestionProps = Omit<MessageProps, 'content' | 'isLink' | 'isHtml'>

export const BotSuggestion: React.FC<BotSuggestionProps> = ({message, itemId, ...rest}) => {
    const {text = ''} = message
    const isHtml = isHTMLSuggestion(message)
    const isLink = isLinkSuggestion(message)

    const content = isLink
        ? parseTranscriptLinksAsHTML({
            text: message.text,
            target: itemId,
            url: message.suggestionURL,
        })
        : text


    return (
        <Message
            message={message}
            isHtml={isHtml}
            isLink={isLink}
            content={content}
            itemId={itemId}
            {...rest}
        />
    )
}
