import type {FC} from 'react'
import React, {useEffect} from 'react'
import {DndProvider} from 'react-dnd'
import {HTML5Backend} from 'react-dnd-html5-backend'

import {useUpdateCurrentTabState} from '@bpinc/ad-current-tab-data-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {useInteractionChatData} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractionBy, useInteractionStateSelector} from '@bpinc/ad-interaction-state-context'
import {getAssociatedChatItem, getInteractionStepId, getServiceId} from '@bpinc/ad-interaction-state-methods'
import {useMainPageUIState} from '@bpinc/ad-main-page-ui-context'
import {
    ChatForm,
    useBotSuggestionsLinksAutoOpener,
    VoiceTranscript,
    VoiceTranscriptStateProvider,
} from '@bpinc/lib-chat-ui'
import {Notifications} from '@bpinc/lib-notifications-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SelectionContainer} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ContactInfo} from './ContactInfo'
import {RightPanel} from './RightPanel'


const contentSizeStyles = tcn('min-w-[580px] max-w-[960px]')


export const VoiceTranscriptPage: FC = () => {
    const {
        kbFloatingState,
        setMainContentNode,
    } = useMainPageUIState(['kbFloatingState', 'setMainContentNode'])
    const queryParams = new URLSearchParams(window.location.search)
    const interactionStepId = queryParams.get('interactionStepId') ?? ''

    const item = useInteractionBy(
        'interactionStepId',
        interactionStepId,
    )

    // If there are 2 associated items, chatItem will be the one, which is chat
    const chatItem = useInteractionStateSelector(state => {
        return getAssociatedChatItem({item, items: state.items})
    })

    const itemId = item?.id ?? ''
    const chatItemId = chatItem?.id ?? ''

    const voiceTranscript = item?.voiceTranscript
    const botSuggestions = item?.botSuggestions

    const {formatFullName} = useFormatting()
    const {t} = useLocalizationState()

    const firstName = item?.displayData.firstName
    const lastName = item?.displayData.lastName
    const phoneNumber = item?.displayData.number
    const fullName = formatFullName(firstName, lastName)


    const chatData = useInteractionChatData(getInteractionStepId(chatItem))

    useBotSuggestionsLinksAutoOpener(itemId, botSuggestions, true)

    const updateCurrentTabState = useUpdateCurrentTabState()

    useEffect(() => {
        updateCurrentTabState({
            app: 'avt',
            data: {
                itemId,
            },
        })
    }, [itemId, updateCurrentTabState])


    if (!itemId || !chatItemId) {
        return <h1>No interaction detected</h1>
    }

    return (
        <VoiceTranscriptStateProvider>
            {/* Required for KB */}
            <DndProvider backend={HTML5Backend}>
                <SelectionContainer autoScroll className="flex h-full flex-col">
                    <Notifications />
                    <header
                        className={`
                            flex 
                            h-16 
                            items-center
                            bg-[color:var(--c-main-bars-bg)]
                            px-6 
                            text-2xl 
                            text-primary-contrast
                        `}
                    >
                        {t.EXTERNAL_CHAT.voice_transcript_header(fullName)}
                    </header>
                    <div className="relative flex flex-auto">
                        <section
                            ref={(el: HTMLDivElement) => {
                                setMainContentNode(el)
                            }}
                            className={tcn(
                                'flex flex-auto flex-col py-4',
                                kbFloatingState === 'floating'
                                    ? 'items-start'
                                    : 'items-center',
                            )}
                        >
                            <div
                                className={tcn(
                                    contentSizeStyles,
                                    'flex w-full flex-auto flex-col',
                                )}
                            >
                                <header className="align-center flex pl-6">
                                    <ContactInfo
                                        firstName={firstName}
                                        lastName={lastName}
                                        fullName={fullName}
                                        contactId={item?.contactId}
                                        phoneNumber={phoneNumber}
                                    />
                                </header>
                                <VoiceTranscript
                                    className="flex h-0 flex-auto flex-col"
                                    filtersClassName="w-[477px] self-end"
                                    voiceTranscript={voiceTranscript}
                                    botSuggestions={botSuggestions}
                                    chatMessages={chatData?.messages}
                                    itemId={itemId}
                                />
                                <ChatForm
                                    itemId={chatItemId}
                                    serviceId={getServiceId(chatItem)}
                                />
                            </div>
                        </section>
                        <RightPanel
                            itemId={itemId}
                            botSuggestions={botSuggestions}
                        />
                    </div>
                </SelectionContainer>
            </DndProvider>
        </VoiceTranscriptStateProvider>
    )
}
