import {useAtomValue, useSetAtom} from 'jotai'

import {platformInfo} from '@bpinc/ad-build-and-platform-info'
import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {useMainPageUIState} from '@bpinc/ad-main-page-ui-context'
import {IconAttachment, IconKnowledgeBase, IconPredefinedMessage, IconSentimentGood} from '@bpinc/lib-jskit-ui'
import {useOpenInteractionSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {moveCursorToTheEnd} from '@bpinc/lib-ui-elements'
import {useKbBridgeMethods} from '@bpinc/state-kb-bridge-provider'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {attachmentButtonElementAtom, chatPopupContentAtom, predefinedSearchInsertPositionAtom} from './atoms'


interface UseControlsOptions {
    itemId: string
    setCurrentMessage: (message: React.SetStateAction<string>) => void
}

export function useControlsOptions({itemId, setCurrentMessage}: UseControlsOptions) {
    const attachmentButton = useAtomValue(attachmentButtonElementAtom)
    const openInteractionSlidingModal = useOpenInteractionSlidingModal()
    const setChatPopupContent = useSetAtom(chatPopupContentAtom)
    const setPredefinedSearchInsertPoisition = useSetAtom(predefinedSearchInsertPositionAtom)
    const t = useLocalizationState('t')
    const isMacOs = platformInfo.platform === 'mac'
    const commandButton = isMacOs ? '' : 'Ctrl'

    const setKBFloatingState = useMainPageUIState('setKBFloatingState')
    const isCommunicator = useIsCommunicator()
    const {setupInteractionKB} = useKbBridgeMethods()

    const openKB = async () => {
        if (isCommunicator) {
            openInteractionSlidingModal(itemId, 'knowledgeBase')
        } else {
            setKBFloatingState(s => s === 'closed' ? 'floating' : s)
            setupInteractionKB()
        }
    }


    return [
        {
            Icon: IconSentimentGood,
            text: t.EXTERNAL_CHAT.add_emoji(),
            shortcut: `${commandButton}E`,
            onClick: () => {
                setChatPopupContent('emojiPicker')
            },
        },
        {
            Icon: IconAttachment,
            text: t.EXTERNAL_CHAT.attachment(),
            shortcut: `${commandButton}A`,
            onClick: () => attachmentButton?.click(),
        },
        {
            Icon: IconPredefinedMessage,
            text: t.EXTERNAL_CHAT.predefined_messages(),
            shortcut: ';',
            onClick: () => {
                const textareaElement = document.querySelector('[data-testid="chat-text-area-input"]')
                if (textareaElement instanceof HTMLElement) {
                    setPredefinedSearchInsertPoisition(textareaElement.innerHTML.length)
                    setCurrentMessage(m => m + ';')
                    textareaElement.focus()

                    setTimeout(() => {
                        moveCursorToTheEnd(textareaElement)
                    })
                }
                setChatPopupContent('predefinedMessages')

            },
        },
        {
            Icon: IconKnowledgeBase,
            text: t.EXTERNAL_CHAT.knowledge_base(),
            shortcut: `${commandButton}K`,
            onClick: openKB,
        },
    ] as const
}
