import type {ChatMessage} from '@bpinc/ad-interaction-message-types'
import type {ChatSessionTranscriptMessage} from '@bpinc/ad-interaction-state-types'
import {normalizeExternalURL} from '@bpinc/lib-helpers'


interface ParseLinkParams {
    text: string
    url: string
    target: string
}
export function parseTranscriptLinksAsHTML({text, url, target}: ParseLinkParams) {
    const parsedUrl = normalizeExternalURL(url)
    return `<a href=${parsedUrl} target="${target}">${text}</a>`
}

export function chatMessageToVoiceTranscript(message: ChatMessage): ChatSessionTranscriptMessage {
    return {
        partyId: message.partyId,
        msgId: message.msgId ?? '',
        text: message.msg ?? '',
        startTime: message.timestamp,
        endTime: message.timestamp,
        sentiment: message.sentiment,
        type: 'chatMessage',
        format: 'HTML',
        isSelf: message.isSelf,
        chatMessageData: message,
    }
}
