import type {ChatMessage, VoiceTranscriptMessageType} from '@bpinc/ad-interaction-message-types'
import {voiceTranscriptMessageTypes} from '@bpinc/ad-interaction-message-types'
import type {VoiceTranscriptCommonMessage, VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'

import type {
    ActivityMessageGroup,
    ChatCommonMessagesType,
    ChatMessageDate,
    ExternalChatMessage,
    ExternalChatMessageWithDate,
    HistoryCallMessage,
    HistoryFileMessage,
    HistoryMessage,
    HistoryOutboundMessagingCampaignMessage,
    HistoryRemoteAssistMessage,
    HistoryVoiceTranscriptMessage,
} from './types'


export function isChatMessageDate(message: ExternalChatMessageWithDate): message is ChatMessageDate {
    return typeof message === 'string'
}

export function isHistoryMessage(message: ExternalChatMessageWithDate | undefined): message is HistoryMessage {
    return !!message && !isChatMessageDate(message) && 'globalInteractionId' in message
}

export function isActivityMessageGroup(message: ExternalChatMessageWithDate | undefined): message is ActivityMessageGroup {
    return !!message && !isChatMessageDate(message) && 'serviceId' in message && message.displayType === 'activityGroup'
}

export function isChatMessage(message: ExternalChatMessageWithDate | undefined): message is ChatMessage {
    return !!message && !isActivityMessageGroup(message) && !isHistoryMessage(message)
}

export function isVoiceTranscriptMessage(
    message: ChatCommonMessagesType | undefined,
): message is VoiceTranscriptCommonMessage {
    return typeof message === 'object' && 'type' in message && voiceTranscriptMessageTypes.includes(message.type as VoiceTranscriptMessageType)
}

export function isExternalChatMessage(
    message: ChatCommonMessagesType,
): message is ExternalChatMessage {
    return typeof message === 'object' && !isVoiceTranscriptMessage(message)
}

export function isExternalChatMessageDate(message: ChatCommonMessagesType | undefined): message is ChatMessageDate {
    return typeof message === 'string'
}

export function isVoiceTranscriptSuggestion(
    message: ChatCommonMessagesType | undefined,
): message is VoiceTranscriptSuggestion {
    return isVoiceTranscriptMessage(message) && message.type === 'suggestion'
}

export function isCallOrVoiceTranscript(message: ExternalChatMessageWithDate | ChatMessageDate | VoiceTranscriptSuggestion): boolean {
    return !isVoiceTranscriptSuggestion(message) && (
        typeof message === 'string' ||
    (message as HistoryMessage).mediaType === 'VOICE' ||
    (message as HistoryMessage).mediaType === 'VOICE_TRANSCRIPT'
    )
}

export function isHistoryCallMessage(message: ExternalChatMessageWithDate | undefined): message is HistoryCallMessage {
    return isHistoryMessage(message) && message.mediaType == 'VOICE'
}

export function isHistoryEmailMessage(message: ExternalChatMessageWithDate | undefined): message is HistoryCallMessage {
    return isHistoryMessage(message) && message.mediaType == 'EMAIL'
}

export function isHistoryVoiceTranscriptMessage(
    message: ExternalChatMessageWithDate | undefined,
): message is HistoryVoiceTranscriptMessage {
    return isHistoryMessage(message) && message.mediaType === 'VOICE_TRANSCRIPT'
}

export function isHistoryRemoteAssistMessage(
    message: ExternalChatMessageWithDate | undefined,
): message is HistoryRemoteAssistMessage {
    return isHistoryMessage(message) && message.mediaType == 'REMOTE_ASSIST'
}

export function isHistoryOutboundMessagingCampaignMessage(
    message: ExternalChatMessageWithDate | undefined,
): message is HistoryOutboundMessagingCampaignMessage {
    return isHistoryMessage(message) && (message as HistoryOutboundMessagingCampaignMessage).serviceType == 'OUTBOUND_MESSAGING'
}

export function isHistoryFileMessageMessage(
    message: ExternalChatMessageWithDate | undefined,
): message is HistoryFileMessage {
    return isHistoryMessage(message) && (message as HistoryFileMessage).displayType === 'file'
}

