import type {CSSProperties, ReactNode} from 'react'
import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'

import {useInteractionChatDataSelector, useInteractionChatStateMethods} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {stringToHTMLFragment, useBoolean} from '@bpinc/lib-helpers'
import {isHTMLElement} from '@bpinc/lib-html-utils'
import {IconUnmask} from '@bpinc/lib-jskit-ui'
import {Popup} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {isChatMessage, isExternalChatMessageDate, isVoiceTranscriptMessage} from '../../../types'
import {MessageAction} from '../../MessageAction'
import {useChatMessages, useChatScrollContainer} from './ChatMessagesContext'
import type {UniqueMessageId} from './useSelectionActions'
import {composedMessageUniqueId, equalUniqueMessageId, getElementMessageId} from './useSelectionActions'


interface WithUnmaskActionProps {
    interactionId: string
    className?: string
    children: ReactNode
}


export const WithUnmaskAction: React.FC<WithUnmaskActionProps> = React.memo(({interactionId, children, className}) => {
    const {t} = useLocalizationState()
    const interactionStepId = useInteractionProp(interactionId, getInteractionStepId)
    const messages = useChatMessages()
    const {value: show, toggle: toggleShow} = useBoolean()
    const unmaskPopupWrapperRef = useRef<HTMLDivElement>(null)
    const [popupRect, setPopupRect] = useState<DOMRectReadOnly>()
    const [unmaskChunkId, setUnmaskChunkId] = useState<string>()
    const [unmaskMessageId, setUnmaskMessageId] = useState<UniqueMessageId>()

    const maskedMessageNodeRef = useRef<HTMLElement | null>(null)

    const {maskMessage} = useInteractionChatStateMethods()
    const masked = useInteractionChatDataSelector(interactionStepId, s => s?.masked)

    const scrollContainer = useChatScrollContainer()

    useEffect(() => {
        const unmaskPopupWrapperNode = unmaskPopupWrapperRef.current
        unmaskPopupWrapperNode?.addEventListener('click', togglePopup)

        function togglePopup(e: MouseEvent) {
            const maskedMessageNode = e.target
            if (!maskedMessageNode || !isHTMLElement(maskedMessageNode)) {
                return
            }
            maskedMessageNodeRef.current = maskedMessageNode

            if (maskedMessageNode.classList.contains('bp-masked') && maskedMessageNode.id) {
                setUnmaskChunkId(maskedMessageNode.id)
                const unmaskElementMessageId = getElementMessageId(maskedMessageNode)

                if (unmaskElementMessageId) {
                    setUnmaskMessageId(unmaskElementMessageId)
                }

                setPopupRect(maskedMessageNode.getBoundingClientRect())

                toggleShow()
            }
        }

        return () => {
            unmaskPopupWrapperNode?.removeEventListener('click', togglePopup)
        }
    }, [toggleShow])

    const handleUnmask = useCallback(
        function handleUnmaskCallback() {
            const messageToUnmask = messages.find(
                msg =>
                    unmaskMessageId &&
                    !isVoiceTranscriptMessage(msg) &&
                    equalUniqueMessageId(unmaskMessageId, composedMessageUniqueId(msg)),
            )

            if (!unmaskChunkId || !unmaskMessageId || isVoiceTranscriptMessage(messageToUnmask) || isExternalChatMessageDate(messageToUnmask)) {
                return
            }

            if (isChatMessage(messageToUnmask)) {
                const unmaskedTextChunk = unmaskMessageId?.messageId
                    ? masked?.messages?.[unmaskMessageId.messageId]?.textChunks?.[unmaskChunkId]?.originalText
                    : undefined

                if (!unmaskedTextChunk) {
                    toggleShow(false)
                    return
                }

                const unmaskedTextChunkHTML = stringToHTMLFragment(unmaskedTextChunk)

                maskedMessageNodeRef.current?.replaceWith(unmaskedTextChunkHTML)

                const maskedText =
                    unmaskPopupWrapperRef.current?.querySelector(`[data-message-id="${messageToUnmask.msgId}"]`)
                        ?.children[0]?.innerHTML ?? ''

                if (!maskedText) {
                    toggleShow(false)
                    return
                }

                maskMessage({
                    itemId: interactionStepId,
                    messageId: unmaskMessageId.messageId!,
                    maskedText,
                })
            }
            toggleShow(false)
        },
        [interactionStepId, maskMessage, masked?.messages, messages, toggleShow, unmaskChunkId, unmaskMessageId],
    )

    const coordinates = useMemo<CSSProperties>(
        function memoizePopupCoordinates() {
            return {
                position: 'fixed',
                top: popupRect?.y ?? 0,
                left: popupRect?.x ?? 0,
            }
        },
        [popupRect],
    )

    return (
        <>
            <div ref={unmaskPopupWrapperRef} className={className}>{children}</div>
            <Popup
                show={show}
                data-testid="selection-popup"
                placement="top-start"
                contentClassName="border-none bg-transparent shadow-none p-0"
                data-visible={show}
                onClickOutside={toggleShow}
                style={coordinates}
                mountNode={scrollContainer}
                content={
                    <MessageAction
                        onClick={handleUnmask}
                        data-testid="unmask-content-button"
                    >
                        <IconUnmask />
                        {t.EMAIL.button_unMaskSelectedText_text()}
                    </MessageAction>
                }
            />
        </>
    )
})
