import {useCallback, useMemo, useRef, useState} from 'react'
import produce from 'immer'

import {useOnItemRemoved} from '@bpinc/ad-interaction-state-hooks'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useOnLogout} from '@bpinc/ad-session-context'
import {useOpenChannel} from '@bpinc/lib-helpers-ui'

import type {
    ChannelOption,
    ChannelState,
    SelectedChannel,
    SelectedPhone,
    Touched,
} from './types'
import {useOptions} from './useOptions'


//TODO: Check if we need this initialOption or we can throw an error if it's not provided
export const initialOption: ChannelOption = {value: 'sms', text: 'SMS'}

export function useChannel(): ChannelState {
    const channelOptions = useOptions()
    const openChannel = useOpenChannel()

    const channelOptionsRef = useRef(channelOptions)
    channelOptionsRef.current = channelOptions

    //------------Select logic------------//
    const [selectedChannel, setSelectedChannel] = useState<SelectedChannel>({})
    const [selectedPhone, setSelectedPhone] = useState<SelectedPhone>({})

    const getSelectedChannel = useCallback((itemId: string) => {
        if (selectedChannel[itemId]) {
            return selectedChannel[itemId]
        }

        if (channelOptions) {
            return channelOptions[0]
        }

        return undefined
    }, [channelOptions, selectedChannel])

    const getSelectedPhone = useCallback((itemId: string) => {
        const channel = getSelectedChannel(itemId)
        if (channel?.value !== 'sms') {
            return undefined
        }

        if (selectedPhone[itemId]) {
            return selectedPhone[itemId]
        }

        if (channel) {
            return channel?.phones?.[0]
        }
    }, [getSelectedChannel, selectedPhone])

    const selectCurrentItemChannel = useCallback(
        function selectCurrentItemChannelCallback(itemId: string, channel: ChannelOption, phoneId?: string) {
            setSelectedChannel(
                produce(channels => {
                    channels[itemId] = channel
                }),
            )
            setSelectedPhone(produce(phones => {
                phones[itemId] = phoneId ? channel.phones?.find(p => p.id === phoneId) : undefined
            }))

            return channel
        },

        [],
    )
    //------------Select logic end------------//

    //------------Reset logic------------//
    const resetChannel = useCallback(function resetChannelCallback() {
        setSelectedChannel({'': initialOption})
    }, [])

    const resetSelectedChannel = useCallback(function deleteChannelCallback(item: Item) {
        setSelectedChannel(
            produce(channels => {
                delete channels[item.id]
            }),
        )
    }, [])


    const [touchedState, setTouchedState] = useState<Touched>({})

    const isTouched = useCallback((itemId: string) => {
        return touchedState[itemId || '']
    }, [touchedState])

    const setIsTouched = useCallback(
        (itemId: string, touched: boolean) => {
            setTouchedState(
                produce(draft => {
                    draft[itemId] = touched
                }),
            )
        },
        [],
    )

    function resetAllTouched() {
        setTouchedState({})
    }

    function resetTouched(item: Item) {
        setTouchedState(
            produce(draft => {
                delete draft[item.id]
            }),
        )
    }


    useOnLogout(() => {
        resetChannel()
        resetAllTouched()
    })
    useOnItemRemoved(item => {
        resetSelectedChannel(item)
        resetTouched(item)
    })


    const setSelectedChannelImpl = useCallback(
        function setSelectedChannelCallback(itemId: string, channel: ChannelOption, phoneId?: string) {
            openChannel(channel.value)
            selectCurrentItemChannel(itemId, channel, phoneId)
            setIsTouched(itemId, true)
        },
        [openChannel, selectCurrentItemChannel, setIsTouched],
    )

    return useMemo(
        function memoizedChannelState() {
            return {
                getSelectedChannel,
                getSelectedPhone,
                setSelectedChannel: setSelectedChannelImpl,
                channelOptions,
                isTouched,
                setIsTouched,
            }
        },
        [getSelectedChannel, getSelectedPhone, setSelectedChannelImpl, channelOptions, isTouched, setIsTouched],
    )
}
