import type {ChangeEvent, FC} from 'react'
import React, {useCallback, useEffect, useMemo, useState} from 'react'
import {debounce} from 'lodash-es'

import {
    ButtonIcon,
    IconIntentFilled,
    IconMessageExternal,
    IconVoiceTranscript,
    SearchField,
} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SearchResultsSwitcher} from '@bpinc/lib-ui-elements'
import {useInteractionUIChatScrollerApi} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {VoiceTranscriptMessageContentMap, VoiceTranscriptSearchResultType} from '../../../types'
import {useVoiceTranscriptUIState} from './VoiceTranscriptContext'


type FiltersProps = {
    className?: string
    messagesContent: VoiceTranscriptMessageContentMap
    itemId: string
}

export const Filters: FC<FiltersProps> = ({className, messagesContent, itemId}) => {
    const {t} = useLocalizationState()
    const {
        search,
        setSearch,
        setSearchResult,
        showBotSuggestions,
        showSMS,
        showTranscript,
        toggleBotSuggestions,
        toggleSMS,
        toggleTranscript,
    } = useVoiceTranscriptUIState()

    const [searchLocal, setSearchLocal] = useState(search)
    const [highlightIndex, setHighlightIndex] = useState<number | undefined>(undefined)

    const scrollerApi = useInteractionUIChatScrollerApi(itemId)

    const setDebouncedSearch = useMemo(() => debounce((innerValue: string) => {
        setSearch(innerValue)
    }, 500), [setSearch])

    const filteredResults = useMemo(
        function memoizeVisibleMessages() {
            if (!search) {
                return []
            }
            return Object.entries(messagesContent).reduce<VoiceTranscriptSearchResultType[]>((result, [messageId, {marksCount}]) => {
                const markers = Array.from({length: marksCount}).map((_, index) => ({
                    messageId,
                    index,
                })).reverse()
                return [...markers, ...result]
            }, [])

        },
        [search, messagesContent],
    )

    useEffect(() => {
        setDebouncedSearch(searchLocal)
    }, [searchLocal, setDebouncedSearch])

    useEffect(() => {
        if (highlightIndex) {
            const searchResult = filteredResults[highlightIndex - 1]
            setSearchResult(searchResult)
            if (searchResult && searchResult.messageId) {
                scrollerApi?.scrollTo(searchResult.messageId, 'center')
            }
        } else {
            setSearchResult(undefined)
        }
    }, [filteredResults, scrollerApi, highlightIndex, setSearchResult])


    const handleHighlight = useCallback(
        function handleHighlightedMessageCallback(position: number) {
            setHighlightIndex(position)
        },
        [],
    )

    const handleSearch = useCallback(
        function handleSearchCallback(evt: ChangeEvent<HTMLInputElement>) {
            setSearchLocal(evt.target.value)
            setHighlightIndex(1)
        },
        [],
    )

    return (
        <div className={tcn('flex items-center gap-2 px-4 py-2 bg-primary-background', className)}>
            <SearchField
                value={searchLocal}
                onChange={handleSearch}
                layoutClassName="grow"
                placeholder={t.EXTERNAL_CHAT.search_transcript()}
            />
            {!!searchLocal &&
              <SearchResultsSwitcher current={highlightIndex || 0} total={filteredResults.length} onChange={handleHighlight} />
            }
            <div className="flex items-center gap-0.5">
                <ButtonIcon
                    size="40"
                    disabled={!showBotSuggestions}
                    onClick={toggleBotSuggestions}
                    className="rounded-xs"
                    tooltip={t.VOICE_TRANSCRIPT.bot_suggestion_btn_tooltip()}
                >
                    <IconIntentFilled className="size-6 icon-disabled" />
                </ButtonIcon>
                <ButtonIcon
                    size="40"
                    disabled={!showSMS}
                    onClick={toggleSMS}
                    className="rounded-xs"
                    tooltip={t.VOICE_TRANSCRIPT.sms_btn_tooltip()}
                >
                    <IconMessageExternal className="size-6 icon-disabled" />
                </ButtonIcon>
                <ButtonIcon
                    size="40"
                    tooltip={t.VOICE_TRANSCRIPT.transcript_btn_tooltip()}
                    disabled={!showTranscript}
                    onClick={toggleTranscript}
                    className="rounded-xs"
                >
                    <IconVoiceTranscript className="size-6 icon-disabled" />
                </ButtonIcon>
            </div>
        </div>
    )
}
