import React from 'react'

import {useContactsStateMethods} from '@bpinc/ad-contacts-state-context'
import {maskActivityHistoryMessages} from '@bpinc/ad-contacts-state-methods'
import {useIsCommunicator} from '@bpinc/ad-current-tab-data-context'
import {
    useInteractionChatDataSelector,
    useInteractionChatStateMethods,
} from '@bpinc/ad-interaction-chat-state-context'
import type {MaskedTextChunks} from '@bpinc/ad-interaction-chat-state-types'
import {getContactId, getInteractionStepId, isAfterCallWork} from '@bpinc/ad-interaction-state-methods'
import type {Item} from '@bpinc/ad-interaction-state-types'
import {useMainPageUIState} from '@bpinc/ad-main-page-ui-context'
import {bpLogger} from '@bpinc/ad-persistent-logger'
import {useSessionState} from '@bpinc/ad-session-context'
import type {SelectionData, Toggle} from '@bpinc/lib-helpers'
import {getErrorMessage, Mask, stringToHTMLFragment} from '@bpinc/lib-helpers'
import {IconAddToCopy, IconCopy, IconKnowledgeBase, IconMask} from '@bpinc/lib-jskit-ui'
import {Notifications, useNotificationsState} from '@bpinc/lib-notifications-context'
import {useOpenInteractionSlidingModal} from '@bpinc/lib-sliding-modal-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {uuid} from '@bpinc/lib-uuid'
import {useKbBridgeMethods} from '@bpinc/state-kb-bridge-provider'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {getExternalChatMessageDisplayType} from '../../../helpers'
import type {ChatCommonMessagesType} from '../../../types'
import {isChatMessage, isExternalChatMessageDate, isHistoryMessage, isVoiceTranscriptMessage} from '../../../types'
import {MessageAction} from '../../MessageAction'
import {
    useChatGetClipboardBuffer,
    useChatMessages,
    useChatSelectedMessages,
    useChatSetClipboardBuffer,
} from './ChatMessagesContext'
import {useGetFormattedClipboardMessage} from './useGetFormattedClipboardMessage'


interface GetSelectionPopupContentParams {
    selectedText: string
    toggleShow: Toggle
    selectionData: SelectionData | undefined
}

export function useSelectionActions(item: Item, withMask = false) {
    const {t} = useLocalizationState()
    const messages = useChatMessages()
    const selectedMessages = useChatSelectedMessages()
    const {serverOrigin, loginId, sessionId, privileges} = useSessionState()
    const {updateActivityHistoryMessageText} = useContactsStateMethods()
    const getFormatClipboardMessage = useGetFormattedClipboardMessage()
    const {showNotification} = useNotificationsState('methods')
    const {maskMessage} = useInteractionChatStateMethods()
    const setClipboardBuffer = useChatSetClipboardBuffer()
    const getClipboardBuffer = useChatGetClipboardBuffer()

    const masked = useInteractionChatDataSelector(
        getInteractionStepId(item),
        s => s?.masked,
    )

    const openInteractionSlidingModal = useOpenInteractionSlidingModal()

    const isCommunicator = useIsCommunicator()
    const setKBFloatingState = useMainPageUIState('setKBFloatingState')
    const {setupInteractionKB} = useKbBridgeMethods()

    return function getSelectionPopupContent({
        toggleShow,
        selectedText,
        selectionData,
    }: GetSelectionPopupContentParams) {
        const {range} = selectionData ?? {}
        const uniqueMessageId = getRangeMessageId(range)

        const selectedMessage = messages.find(
            message =>
                uniqueMessageId &&
                equalUniqueMessageId(
                    uniqueMessageId,
                    composedMessageUniqueId(message),
                ),
        )

        function canMask() {
            if (
                !withMask ||
                isExternalChatMessageDate(selectedMessage) ||
                !privileges.CAN_MASK_INTERACTION_CONTENT
            ) {
                return false
            }

            if (!isVoiceTranscriptMessage(selectedMessage)) {
                const displayType =
                    getExternalChatMessageDisplayType(selectedMessage)

                return displayType === 'message'
            }

            return false
        }
        function handleMaskClick() {
            if (
                isVoiceTranscriptMessage(selectedMessage) ||
                isExternalChatMessageDate(selectedMessage)
            ) {
                return
            }

            if (isHistoryMessage(selectedMessage)) {
                //Activity history message (both in active chat session and AH)
                const maskedMessageBeforeMasking = getMaskedText(
                    composedMessageUniqueId(selectedMessage),
                )

                maskSelectedHistory(range)

                const maskedText = getMaskedText(
                    composedMessageUniqueId(selectedMessage),
                )

                maskActivityHistoryMessages({
                    loginId,
                    sessionId,
                    serverOrigin,
                    globalInteractionId: selectedMessage.globalInteractionId,
                    messages: [
                        {
                            messageId: selectedMessage.msgId,
                            maskedText,
                        },
                    ],
                })
                    .then(() => {
                        const contactId = getContactId(item)

                        if (contactId) {
                            updateActivityHistoryMessageText({
                                contactId,
                                messageId: selectedMessage.msgId,
                                messageText: maskedText,
                            })
                        }
                    })
                    .catch(err => {
                        //TODO: Ask for real error message text
                        showNotification({
                            type: 'error',
                            id: Notifications.ChatMessageMaskError,
                            tags: ['current-view'],
                            message:
                                t.EXTERNAL_CHAT.masking_failed_error_message(),
                            duration: 5000,
                        })
                        bpLogger.error(getErrorMessage(err))

                        const selectedMessageNode = getMessageTextElement(
                            composedMessageUniqueId(selectedMessage),
                        )
                        if (selectedMessageNode) {
                            selectedMessageNode.innerHTML =
                                maskedMessageBeforeMasking
                        }
                    })

                //Active chat session, current chat message
            } else if (
                isChatMessage(selectedMessage) &&
                selectedMessage.msgId
            ) {
                const textChunks =
                    masked?.messages?.[selectedMessage.msgId]?.textChunks
                const updatedTextChunks = maskSelectedText(range, textChunks)
                const maskedText = getMaskedText(
                    composedMessageUniqueId(selectedMessage),
                )

                maskMessage({
                    itemId: getInteractionStepId(item),
                    messageId: selectedMessage.msgId,
                    maskedText,
                    textChunks: updatedTextChunks,
                })
            }

            toggleShow(false)
        }
        async function handleSearchKnowledgeBaseClick() {
            toggleShow(false)

            if (isCommunicator) {
                openInteractionSlidingModal(item.id, 'knowledgeBase')
            } else {
                setKBFloatingState(s => (s === 'closed' ? 'floating' : s))
            }
            setupInteractionKB(selectedText)
        }
        function handleCopyClick() {
            toggleShow(false)

            let textToCopy = ''
            let textToAddToCopyBuffer = ''

            //If several messages selected, copy all of them
            if (selectedMessages.length > 1) {
                textToCopy = selectedMessages.reduce<string>((res, message) => {
                    return res + getFormatClipboardMessage(message)
                }, '')
                textToAddToCopyBuffer = textToCopy
            //Otherwise copy only selected message, but add formatted message to "Add to copy" buffer
            } else {
                assertDefined(selectedMessages[0])
                textToCopy = selectedText
                textToAddToCopyBuffer = getFormatClipboardMessage(selectedMessages[0])
            }

            console.info(' Clipboard text\n', textToCopy)
            setClipboardBuffer(textToAddToCopyBuffer)
            navigator.clipboard.writeText(textToCopy)
        }

        function handleAddToCopy() {
            toggleShow(false)

            let textToAddToCopyBuffer = ''

            //More than one message selected, add all of them to "Add to copy" buffer
            if (selectedMessages.length > 1) {
                textToAddToCopyBuffer = selectedMessages.reduce<string>(
                    (res, message) => {
                        return res + getFormatClipboardMessage(message)
                    },
                    '',
                )
            //Only one message selected, add it to "Add to copy" buffer
            } else {
                assertDefined(selectedMessages[0])
                textToAddToCopyBuffer = getFormatClipboardMessage(selectedMessages[0])
            }

            const addToCopyBuffer = getClipboardBuffer()
            const newClipboardData = addToCopyBuffer + textToAddToCopyBuffer
            console.info(' Clipboard text\n', newClipboardData)

            navigator.clipboard.writeText(newClipboardData)
            setClipboardBuffer(newClipboardData)
        }

        if (!selectedText) {
            return null
        }

        return (
            <>
                {!isAfterCallWork(item) && (
                    <MessageAction
                        onClick={handleSearchKnowledgeBaseClick}
                        data-testid="search-kb-button"
                    >
                        <IconKnowledgeBase className="size-4" />
                        {t.GLOBAL.search()}
                    </MessageAction>
                )}
                {canMask() && (
                    <MessageAction
                        onClick={handleMaskClick}
                        data-testid="mask-content-button"
                    >
                        <IconMask />
                        {t.EXTERNAL_CHAT.mask()}
                    </MessageAction>
                )}
                <MessageAction onClick={handleCopyClick} data-testid="copy">
                    <IconCopy />
                    {t.GLOBAL.copy()}
                </MessageAction>
                {!!getClipboardBuffer().length && (
                    <MessageAction
                        onClick={handleAddToCopy}
                        data-testid="add-to-copy"
                    >
                        <IconAddToCopy />
                        {t.GLOBAL.add_to_copy()}
                    </MessageAction>
                )}
            </>
        )
    }
}

function getRangeMessageId(
    range: Range | undefined,
): UniqueMessageId | undefined {
    const commonElement = getCommonAncestorElement(range)
    return getElementMessageId(commonElement)
}

export function getElementMessageId(
    element: Element | null | undefined,
): UniqueMessageId | undefined {
    const messageElement = element?.closest(uniqueMessageIdSelector())
    const messageId = messageElement?.getAttribute('data-message-id')
    let globalInteractionId =
        messageElement?.getAttribute('data-global-interaction-id') ?? undefined
    if (globalInteractionId === 'current') {
        globalInteractionId = undefined
    }
    if (messageId) {
        return {messageId, globalInteractionId}
    } else {
        return undefined
    }
}

function getCommonAncestorElement(range: Range | undefined) {
    return range?.commonAncestorContainer.nodeType === Node.ELEMENT_NODE
        ? (range?.commonAncestorContainer as Element)
        : range?.commonAncestorContainer.parentElement
}

function maskSelectedHistory(range: Range | undefined) {
    if (!range) {
        return
    }

    let fragment = range.extractContents()
    const tempSpan = getFragmentWithinSpanNode(fragment)

    Mask.obscureHTML(tempSpan)

    fragment = document.createDocumentFragment()

    Array.from(tempSpan.childNodes).forEach(node => {
        fragment.appendChild(node)
    })

    range.insertNode(fragment)
}

function maskSelectedText(
    range: Range | undefined,
    maskedTextChunks: MaskedTextChunks = {},
) {
    const updatedMaskedTextChunks = {...maskedTextChunks}

    if (!range) {
        return
    }

    extendUnmasked(range)

    const fragment = range.extractContents()
    const span = getFragmentWithinSpanNode(fragment)
    const unmaskedSpan = revealUnmasked(span, updatedMaskedTextChunks)
    const id = uuid()

    unmaskedSpan.classList.add('bp-masked')
    unmaskedSpan.id = id

    const unmaskedText = unmaskedSpan.innerHTML

    Mask.obscureHTML(unmaskedSpan)

    updatedMaskedTextChunks[id] = {
        originalText: unmaskedText,
        maskedText: unmaskedSpan.innerHTML,
    }

    range.insertNode(unmaskedSpan)
    return updatedMaskedTextChunks
}

function getFragmentWithinSpanNode(fragment: DocumentFragment) {
    const span = document.createElement('span')

    fragment.childNodes.forEach(node => {
        const nodeCopy = node.cloneNode(true)
        span.append(nodeCopy)
    })

    return span
}

function extendUnmasked(range: Range) {
    const startElement = range.startContainer.parentElement

    if (isMasked(startElement) || isAnchorTag(startElement)) {
        range.setStartBefore(range.startContainer)
        range.setStartBefore(range.startContainer)
    }

    const endElement = range.endContainer.parentElement

    if (isMasked(endElement) || isAnchorTag(endElement)) {
        range.setEndAfter(range.endContainer)
        range.setEndAfter(range.endContainer)
    }
}

function isMasked(element: HTMLElement | null | undefined) {
    return element?.classList.contains('bp-masked')
}

function isAnchorTag(element: HTMLElement | null | undefined) {
    return element?.nodeName === 'A'
}

function revealUnmasked(html: HTMLElement, maskedTextChunks: MaskedTextChunks) {
    const htmlClone = html.cloneNode(true) as HTMLElement

    revealUnmaskedInner(htmlClone)

    return htmlClone

    function revealUnmaskedInner(htmlInner: HTMLElement) {
        if (htmlInner.classList.contains('bp-masked')) {
            const textChunkId = htmlInner.id
            const unmasked = stringToHTMLFragment(
                maskedTextChunks[textChunkId].originalText,
            )

            delete maskedTextChunks[htmlInner.id]

            htmlInner.replaceWith(unmasked)
            return
        }

        Array.from(htmlInner.children).forEach(htmlElement => {
            revealUnmaskedInner(htmlElement as HTMLElement)
        })
    }
}

export interface UniqueMessageId {
    globalInteractionId: string | undefined
    messageId: string | undefined
}

export function equalUniqueMessageId(
    id1: UniqueMessageId,
    id2: UniqueMessageId,
) {
    return (
        id1.messageId === id2.messageId &&
        id1.globalInteractionId === id2.globalInteractionId
    )
}

export function composedMessageUniqueId(
    composedChatMessage: ChatCommonMessagesType,
): UniqueMessageId {
    return {
        messageId: isExternalChatMessageDate(composedChatMessage)
            ? composedChatMessage
            : composedChatMessage.msgId,
        globalInteractionId:
            !isExternalChatMessageDate(composedChatMessage) &&
            !isVoiceTranscriptMessage(composedChatMessage) &&
            isHistoryMessage(composedChatMessage)
                ? composedChatMessage.globalInteractionId
                : undefined,
    }
}

function getMaskedText(uniqueMessageId: UniqueMessageId) {
    return getMessageTextElement(uniqueMessageId)?.innerHTML ?? ''
}

function getMessageTextElement(uniqueMessageId: UniqueMessageId) {
    return uniqueMessageId.messageId
        ? document.querySelector(uniqueMessageIdSelector(uniqueMessageId))
            ?.children[0]
        : undefined
}

function uniqueMessageIdSelector(uniqueMessageId?: UniqueMessageId): string {
    if (uniqueMessageId) {
        const {messageId, globalInteractionId} = uniqueMessageId
        return `[data-message-id="${messageId}"][data-global-interaction-id="${
            globalInteractionId ?? 'current'
        }"]`
    } else {
        return `[data-message-id][data-global-interaction-id]`
    }
}
