import {useMemo} from 'react'
import {escapeRegExp} from 'lodash-es'

import {normalizeByUrlParser} from '@bpinc/ad-interaction-state-hooks'
import type {VoiceTranscriptCommonMessage} from '@bpinc/ad-interaction-state-types'
import {isCurrentChatTranscriptMessage, isHTMLSuggestion, isLinkSuggestion} from '@bpinc/ad-interaction-state-types'
import {highlightHTML} from '@bpinc/lib-html-utils'

import {getMessageId} from '../../../helpers'
import type {VoiceTranscriptMessageContentMap} from '../../../types'
import {parseTranscriptLinksAsHTML} from './utils'
import {useVoiceTranscriptUIState} from './VoiceTranscriptContext'

import styles from './VoiceTranscript.module.less'


export function useVoiceTranscriptMessagesContentMap(messages: VoiceTranscriptCommonMessage[], itemId: string) {
    const {search} = useVoiceTranscriptUIState()

    return useMemo(() => {
        return messages.reduce<VoiceTranscriptMessageContentMap>((result, message) => {
            const {format = 'TEXT'} = message
            const id = getMessageId(message)
            let text = message.text

            const isCurrentChatMessage = isCurrentChatTranscriptMessage(message)
            if (isCurrentChatMessage) {
                text = normalizeByUrlParser(message.text)
            }

            const parsedText = format === 'TEXT'
                ? text
                : parseTranscriptLinksAsHTML({
                    text: message.text,
                    target: itemId,
                    url: message.suggestionURL || '',
                })


            const content = highlightHTML({html: parsedText, textToHighlight: search, className: styles.mark})
            const marksCount = (content.match(new RegExp(escapeRegExp(styles.mark), 'g')) || []).length

            result[id] = {
                content,
                marksCount,
                isLink: isLinkSuggestion(message),
                isHTML: isHTMLSuggestion(message),
            }

            return result
        }, {})
    }, [itemId, messages, search])
}
