import type {ReactElement, ReactFragment} from 'react'
import React from 'react'
import {createContext} from 'use-context-selector'

import {useOnItemRemoved} from '@bpinc/ad-interaction-state-hooks'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import type {ChatAttachment} from '@bpinc/ad-request-urls'
import {createUIStateSelectorHook} from '@bpinc/ad-state-context-factory'
import {useUploadChatFile} from '@bpinc/ad-upload-methods'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'

import {defaultState} from './constants'
import type {AttachmentsState, RequestQueueItem} from './types'
import {useAttachmentsState} from './useAttachmentsState'


const ChatAttachmentsUIContext = createContext <AttachmentsState<ChatAttachment>>(
    defaultState,
)
ChatAttachmentsUIContext.displayName = 'ChatAttachmentsUIContext'

function getFormData({file}: RequestQueueItem) {
    const bodyFormData = new FormData()

    bodyFormData.append('file', file)
    return bodyFormData
}

export const ChatAttachmentsUIStateProvider: FCWithChildren<ReactElement | ReactFragment> = ({children}) => {
    const uploadChatFile = useUploadChatFile()

    const value = useAttachmentsState({uploadFile: uploadChatFile, getFormData})

    useOnItemRemoved(item => {
        value.removeAttachmentsList(getInteractionStepId(item))
    })

    return (
        <ChatAttachmentsUIContext.Provider value={value}>
            {children}
        </ChatAttachmentsUIContext.Provider>
    )
}

export const {useChatAttachmentsUIState} = createUIStateSelectorHook(ChatAttachmentsUIContext, 'chatAttachmentsUIState')
