import {useEffect, useMemo} from 'react'
import {escapeRegExp} from 'lodash-es'

import {useAssociatedChatInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getChannel, hasMultiplyPhones} from '@bpinc/ad-interaction-state-methods'
import {useInteractionId} from '@bpinc/lib-interaction-context'

import {useChannelState} from '../../../channel-context'
import type {ChannelOption} from '../../../channel-context/types'


export function useInitializeDefaultChannelEffect() {
    const itemId = useInteractionId()
    const itemChannel = useAssociatedChatInteractionProp(itemId, getChannel)
    const phoneNumber = useAssociatedChatInteractionProp(itemId, chatActiveItem => chatActiveItem?.displayData?.number)
    const itemHasMultiplyPhones = useAssociatedChatInteractionProp(itemId, hasMultiplyPhones)

    const {isTouched, setIsTouched, channelOptions, setSelectedChannel} = useChannelState()

    //-------Initialize default channel-----//
    /* based on item's multiply phones and own channel */
    const defaultChannel = useMemo(
        function memoizeDefaultChannel() {
            if (itemChannel) {
                return itemHasMultiplyPhones ? getChannelWhenMultiplyPhones() : getChannelWhenOnePhone()
            }

            if (itemHasMultiplyPhones && phoneNumber) {
                return getChannelWhenMultiplyPhones()
            }

            return getSMSChannel()

            function getChannelWhenMultiplyPhones() {
                return (
                    channelOptions.find((item: ChannelOption) =>
                        !itemChannel || itemChannel === 'sms'
                            ? new RegExp(escapeRegExp(phoneNumber) ?? '').test(item.value)
                            : item.value === itemChannel,
                    ) || channelOptions[0]
                )
            }

            function getChannelWhenOnePhone() {
                return channelOptions.find((item: ChannelOption) => item.value === itemChannel)
            }

            function getSMSChannel() {
                return channelOptions.find((item: ChannelOption) => item.value === 'sms')
            }
        },
        [itemChannel, itemHasMultiplyPhones, phoneNumber, channelOptions],
    )

    const currentItemTouched = isTouched(itemId)

    useEffect(function selecteDefaultChannel() {
        if (!currentItemTouched && defaultChannel) {
            setSelectedChannel(itemId, defaultChannel)
        }
    }, [currentItemTouched, defaultChannel, itemId, setIsTouched, setSelectedChannel])

}
