import {useEffect} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {isExternalChat, isInboundInteraction} from '@bpinc/ad-interaction-state-methods'
import {
    usePredefinedChatMessageState,
    usePredefinedChatMessageStateMethods,
} from '@bpinc/ad-predefined-chat-message-state-context'
import {useNotificationsState} from '@bpinc/lib-notifications-context'


interface UseGreetingsParams {
    currentMessage: string
    setCurrentMessage: (message: string) => void
    parseVariables: (message: string) => string
    itemId: string
}

export function useGreetings({currentMessage, setCurrentMessage, itemId, parseVariables}: UseGreetingsParams) {
    const {hideNotificationsByTag} = useNotificationsState('methods')

    const itemIsInboundExternalChat = useInteractionProp(
        itemId,
        item => isExternalChat(item) && isInboundInteraction(item),
    )

    const {greetings} = usePredefinedChatMessageState()
    const {resetGreeting} = usePredefinedChatMessageStateMethods()

    useEffect(() => {
        if (currentMessage) {
            resetGreeting(itemId)
        } else if (greetings[itemId] && itemIsInboundExternalChat) {
            setCurrentMessage(parseVariables(greetings[itemId]))
            resetGreeting(itemId)
            // TODO: investigate which notification we need to hide here
            // hideNotificationsByTag()
        }
    }, [
        itemId,
        currentMessage,
        greetings,
        hideNotificationsByTag,
        parseVariables,
        resetGreeting,
        setCurrentMessage,
        itemIsInboundExternalChat,
    ])
}
