import type {ReactNode} from 'react'
import React from 'react'

import type {ChatCommonMessagesType, ChatScrollNodeType} from '../../../types'
import {ChatMessagesContextProvider} from './ChatMessagesContext'
import {WithSelectionActions} from './WithSelectionActions'


type ChatMessagesSelectionActionsProps = {
    messages: ChatCommonMessagesType[]
    withMaskAction?: boolean
    interactionId: string
    children: ReactNode
    scrollContainer: ChatScrollNodeType
}

export const ChatMessagesSelectionActions: React.FC<ChatMessagesSelectionActionsProps> = ({
    children,
    withMaskAction,
    messages,
    interactionId,
    scrollContainer,
}) => {
    return (
        <ChatMessagesContextProvider messages={messages} interactionId={interactionId} scrollContainer={scrollContainer}>
            <WithSelectionActions withMaskAction={withMaskAction} interactionId={interactionId}>
                {children}
            </WithSelectionActions>
        </ChatMessagesContextProvider>
    )
}
