import type {FC} from 'react'
import React from 'react'

import type {VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'
import {BotSuggestion} from '@bpinc/lib-chat-ui'


interface BotSuggestionsProps {
    itemId: string
    botSuggestions: VoiceTranscriptSuggestion[] | undefined
}

export const BotSuggestions: FC<BotSuggestionsProps> = ({itemId, botSuggestions}) => {
    return (
        <div className="flex h-0 flex-auto flex-col overflow-auto break-words px-4">
            {botSuggestions?.map((suggestion, index) => (
                <BotSuggestion
                    message={suggestion}
                    itemId={itemId}
                    //TODO: Temporary solution. Use suggestion.msgId instead of index
                    key={index}
                    direction="outgoing"
                />
            ))}
        </div>
    )
}
