import {useCallback} from 'react'

import {useBrowserTabMetaInfo} from '@bpinc/ad-current-tab-data-context'
import {useInteractionStateMethods} from '@bpinc/ad-interaction-state-context'
import type {VoiceTranscriptCommonMessage} from '@bpinc/ad-interaction-state-types'
import {useScreenPopStateMethods} from '@bpinc/ad-screen-pop-state-context'
import {normalizeExternalURL} from '@bpinc/lib-helpers'
import {formatHTMLAsOneLineString} from '@bpinc/lib-html-utils'


export function useLinkOpener(itemId: string) {
    const {addTabs} = useScreenPopStateMethods()
    const browserTabInfo = useBrowserTabMetaInfo()
    const appType = browserTabInfo?.app
    const {saveOpenedSuggestionUrl} = useInteractionStateMethods()

    return useCallback(function openLink(suggestion: VoiceTranscriptCommonMessage) {
        if (!suggestion.suggestionURL) {
            return
        }


        saveOpenedSuggestionUrl(itemId, suggestion.suggestionURL)

        if (appType === 'ad' && suggestion.openURLIn === 'AGENT_DESKTOP') {
            const tabId = suggestion.msgId ?? suggestion.suggestionURL

            addTabs(itemId, [
                {
                    id: tabId,
                    type: 'iframe',
                    url: suggestion.suggestionURL,
                    title: formatHTMLAsOneLineString(suggestion.text),
                },
            ])

        } else {
            try {
                window.open(normalizeExternalURL(suggestion.suggestionURL), suggestion.openURLIn == 'NEW_TAB' ? '_blank' : undefined, 'noopener,noreferrer')
            } catch (e) {
                const errorText = e instanceof Error ? e.message : 'unknown error'
                console.error(`error opening window for url ${suggestion.suggestionURL}: ${errorText}`)
            }
        }
    }, [appType, itemId, addTabs, saveOpenedSuggestionUrl])
}
