import type {ReactElement, ReactFragment} from 'react'
import React from 'react'
import {createContext} from 'use-context-selector'

import type {EmailAttachment} from '@bpinc/ad-request-urls'
import {createUIStateSelectorHook} from '@bpinc/ad-state-context-factory'
import {useUploadEmailFile} from '@bpinc/ad-upload-methods'

import {defaultState} from './constants'
import type {AttachmentsState, RequestQueueItem} from './types'
import {useAttachmentsState} from './useAttachmentsState'


const EmailAttachmentsUIContext = createContext <AttachmentsState<EmailAttachment>>(
    defaultState,
)
EmailAttachmentsUIContext.displayName = 'EmailAttachmentsUIContext'

interface EmailAttachmentsUIStateProviderProps {
    children: ReactElement | ReactFragment
}

function getFormData({file, attachmentsGroupId}: RequestQueueItem) {
    const bodyFormData = new FormData()

    bodyFormData.append('file', file)
    bodyFormData.append('email', attachmentsGroupId)

    return bodyFormData
}

export const EmailAttachmentsUIStateProvider: React.FC<EmailAttachmentsUIStateProviderProps> = ({children}) => {
    const uploadEmailFile = useUploadEmailFile()

    const value = useAttachmentsState({uploadFile: uploadEmailFile, getFormData})

    //TODO: Clean state on email draft removed

    return (
        <EmailAttachmentsUIContext.Provider value={value}>
            {children}
        </EmailAttachmentsUIContext.Provider>
    )
}

export const {useEmailAttachmentsUIState} = createUIStateSelectorHook(EmailAttachmentsUIContext, 'emailAttachmentsUIState')
