import type {FC, ReactElement} from 'react'

import {useActiveChatChecker} from '@bpinc/ad-interaction-chat-state-context'
import {IconMask, IconUnmask} from '@bpinc/lib-jskit-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {MessageAction} from '../../MessageAction'
import {useFileMasker} from '../ExternalMessages/messages/FileMessage/useFileMasker'


interface WithAttachmentMaskActionProps {
    fileIsMasked: boolean
    canMask: boolean
    globalInteractionId: string | undefined
    interactionStepId: string
    fileId: string
    contactId: string
    children: ReactElement
}

export const WithAttachmentMaskAction: FC<WithAttachmentMaskActionProps> = ({
    fileIsMasked,
    canMask,
    interactionStepId,
    globalInteractionId,
    fileId,
    contactId,
    children,
}) => {
    const {t} = useLocalizationState()

    const isActiveChat = useActiveChatChecker(interactionStepId)

    const maskFile = useFileMasker({
        interactionStepId,
        itemContactId: contactId,
        globalInteractionId,
        fileId,
        isActiveChat,
    })


    return (
        <div
            className="group relative"
        >
            {fileIsMasked && <IconMask className="absolute left-1/2 top-1/2 z-[1] size-6 -translate-x-1/2 -translate-y-1/2" />}
            {canMask && (
                <MessageAction onClick={maskFile} className="absolute right-2 top-2 z-[1] opacity-0 transition-opacity group-hover:opacity-100">
                    {fileIsMasked ? <IconUnmask /> : <IconMask />}
                    <p>
                        {fileIsMasked
                            ? t.EXTERNAL_CHAT.unmask()
                            : t.EXTERNAL_CHAT.mask()}
                    </p>
                </MessageAction>
            )}
            <div
                className={
                    tcn(
                        'border border-transparent',
                        {
                            'overflow-hidden rounded-xs border-[rgba(0,0,0,0.1)]': fileIsMasked,
                        })
                }
            >
                <div
                    className={tcn({
                        'pointer-events-none blur-[6px] state-layer-bg-white state-layer-mix-dark-on-white-1': fileIsMasked,
                    })}
                >
                    {children}
                </div>
            </div>
        </div>
    )
}
