import type {FC, ReactNode} from 'react'
import React, {useEffect, useRef} from 'react'

import {useVoiceTranscriptUIState} from './VoiceTranscriptContext'

import styles from './VoiceTranscript.module.less'


type SearchResultHighlightProps = {
    messageId: string
    children: ReactNode
}

export const SearchResultHighlight: FC<SearchResultHighlightProps> = ({messageId, children}) => {
    const {searchResult} = useVoiceTranscriptUIState()
    const ref = useRef<HTMLDivElement>(null)

    useEffect(() => {
        if (ref.current && searchResult && searchResult.messageId === messageId) {
            const highlightedMarkElem = ref.current.querySelectorAll<HTMLElement>('mark')?.[searchResult.index]
            highlightedMarkElem.classList.add(styles.highlighted)

            const markElement = ref.current.querySelectorAll<HTMLElement>('mark')?.[searchResult.index]
            markElement?.scrollIntoView({block: 'center'})

            return () => highlightedMarkElem.classList.remove(styles.highlighted)
        }
    }, [messageId, searchResult])

    return (
        <div ref={ref} data-message-highlight={messageId}>
            {children}
        </div>
    )
}
