import React, {useCallback} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import type {VoiceTranscriptCommonMessage} from '@bpinc/ad-interaction-state-types'
import {isCurrentChatTranscriptMessage} from '@bpinc/ad-interaction-state-types'

import {getMessageId} from '../../../helpers'
import type {ChatCommonMessagesType, ChatScrollNodeType, VoiceTranscriptMessageContentMap} from '../../../types'
import {isVoiceTranscriptMessage} from '../../../types'
import {VirtualizedMessages} from '../VirtualizedMessages/VirtualizedMessages'
import {ChatMessage} from './messages/ChatMessage'
import {Message} from './messages/Message'
import {SearchResultHighlight} from './SearchResultHighlight'


type VirtualizedVoiceTranscriptMessagesProps = {
    messages: VoiceTranscriptCommonMessage[]
    messagesContent: VoiceTranscriptMessageContentMap
    scrollNode: ChatScrollNodeType
    itemId: string
    contactId: string
    interactionStepId: string
}

export const VirtualizedVoiceTranscriptMessages = React.memo<VirtualizedVoiceTranscriptMessagesProps>(
    function VirtualizedVoiceTranscriptMessages({
        itemId,
        contactId,
        scrollNode,
        messages,
        interactionStepId,
        messagesContent,
    }) {
        const isDIDChat = useInteractionProp(itemId, item => item?.scenarioData?.isDID ?? false)

        const renderMessage = useCallback((message: ChatCommonMessagesType) => {
            if (!isVoiceTranscriptMessage(message)) {
                return null
            }

            const messageId = getMessageId(message)
            const messageData = messagesContent[messageId]

            if (!messageData) {
                return null
            }

            const direction =
                message.isSelf || message.type === 'suggestion'
                    ? 'outgoing'
                    : 'incoming'

            return (
                <SearchResultHighlight messageId={messageId}>
                    <div data-message="true">
                        {
                            isCurrentChatTranscriptMessage(message) ? (
                                <ChatMessage
                                    isDIDChat={isDIDChat}
                                    scrollNode={scrollNode}
                                    itemContactId={contactId}
                                    itemInteractionStepId={interactionStepId}
                                    direction={direction}
                                    itemId={itemId}
                                    message={message}
                                    content={messageData.content}
                                    isLink={messageData.isLink}
                                    isHtml={messageData.isHTML}
                                />
                            ) : (
                                <Message
                                    direction={direction}
                                    itemId={itemId}
                                    message={message}
                                    content={messageData.content}
                                    isLink={messageData.isLink}
                                    isHtml={messageData.isHTML}
                                />
                            )
                        }
                    </div>
                </SearchResultHighlight>
            )
        }, [contactId, interactionStepId, isDIDChat, itemId, messagesContent, scrollNode])

        return (
            <VirtualizedMessages
                key={`${itemId}_transcript`}
                itemId={itemId}
                contactId={contactId}
                type="transcript"
                messages={messages}
                renderMessage={renderMessage}
                scrollNode={scrollNode}
            />
        )
    })

