import React, {useCallback, useEffect, useMemo, useRef} from 'react'
import type {ListRange, VirtuosoHandle} from 'react-virtuoso'
import {Virtuoso} from 'react-virtuoso'
import produce from 'immer'
import {debounce} from 'lodash-es'

import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {getChannel, getServiceNameFromItem} from '@bpinc/ad-interaction-state-methods'
import {useAsRef} from '@bpinc/lib-helpers'
import {getChannelImageIcon} from '@bpinc/lib-ui-elements'
import {useInteractionUIItemStateSetter} from '@bpinc/state-interaction-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {getMessageId} from '../../../helpers'
import type {ChatCommonMessagesType, ChatScrollNodeType} from '../../../types'
import {isExternalChatMessageDate} from '../../../types'
import {useMessagesLoader} from '../useMessagesLoader'
import {NoMessagesPlaceholder} from './NoMessagesPlaceholder'
import {TypingParties} from './TypingParties'
import {useInitialScrollPosition} from './useInitialScrollPosition'
import {useSaveScrollPositionEffect} from './useSaveScrollPositionEffect'


const VIEWPORT_INCREASE = 200 //Viewport increasing to prevent new messages from blinking

type VirtualizedMessagesProps = {
    messages: ChatCommonMessagesType[]
    scrollNode: ChatScrollNodeType
    itemId: string
    contactId: string
    type: ChatType
    renderMessage: (message: ChatCommonMessagesType) => React.ReactNode
}

export const VirtualizedMessages = React.memo<VirtualizedMessagesProps>(
    function VirtualizedMessages({
        messages,
        itemId,
        type,
        contactId,
        scrollNode,
        renderMessage,
    }) {
        const {t} = useLocalizationState()
        const setItemState = useInteractionUIItemStateSetter()
        const virtuosoRef = useRef<VirtuosoHandle>(null)
        const rangeRef = useRef<ListRange | undefined>(undefined)
        const messagesRef = useAsRef(messages)
        const initialRender = useRef(false)

        const interaction = useInteraction(itemId)
        const serviceName = getServiceNameFromItem(interaction)
        const channel = getChannel(interaction)
        const ChannelIcon = getChannelImageIcon(channel)

        const loadMoreMessages = useMessagesLoader(itemId, contactId, type)

        useEffect(() => {
            setItemState(itemId, produce(s => {
                s.scrollerApi = {
                    scrollToLast: () => {
                        virtuosoRef.current?.scrollToIndex({index: 'LAST'})
                    },
                    scrollTo: (id, align) => {
                        const index = messagesRef.current?.findIndex(m => id === getMessageId(m))
                        virtuosoRef.current?.scrollToIndex({
                            index,
                            align,
                        })
                    },
                }
            }))
        }, [itemId, messagesRef, setItemState])

        useSaveScrollPositionEffect(messages, virtuosoRef, rangeRef, scrollNode)

        const initialScrollPosition = useInitialScrollPosition(itemId, type, messagesRef, rangeRef, scrollNode)
        const generateMessageKey = useCallback((_index: number, message: ChatCommonMessagesType) => getMessageId(message), [])
        const channelTitle = channel ? (
            channel === 'remoteassist'
                ? t.ACTIVITY_HISTORY.remote_assist_title()
                : t.GLOBAL[`chat_type_${channel}`]()
        ) : ''
        const renderHeader = useCallback(() => serviceName ? (
            <div className="my-6 flex justify-center">
                <div className="text-neutral-on-secondary">
                    <p className="text-center font-semibold caption-s">
                        {serviceName}
                    </p>
                    <div className="flex items-center justify-center">
                        {ChannelIcon && <ChannelIcon className="mr-1 size-4 icon-disabled" />}
                        {' '}
                        <p className="text-center caption-s">
                            {channelTitle}
                        </p>
                    </div>
                </div>
            </div>
        ) : null, [ChannelIcon, channelTitle, serviceName])
        const virtuosoComponents = useMemo(() => ({
            Header: renderHeader,
            Footer: () => (
                <TypingParties
                    itemId={itemId}
                    messagesRef={messagesRef}
                    rangeRef={rangeRef}
                />
            ),
        }), [itemId, messagesRef, renderHeader])
        const setListRange = useCallback((r: ListRange) => {
            rangeRef.current = r
        }, [])
        const onHeightChange = useCallback((height: number) => {
            if (scrollNode && scrollNode.clientHeight >= height) {
                loadMoreMessages()
            }
        }, [loadMoreMessages, scrollNode])

        //Debounce is needed because for some reason Virtuoso calls onHeightChange twice
        const debouncedOnHeightChange = useMemo(() => debounce(onHeightChange, 100), [onHeightChange])

        const onTopStateChange = useCallback((atTop: boolean) => {
            if (!initialRender.current) {
                initialRender.current = true
                return
            }
            if (atTop) {
                loadMoreMessages()
            }
        }, [loadMoreMessages])

        const messageRenderer = useCallback((_index: number, message: ChatCommonMessagesType) => {
            return renderMessage(message)
        }, [renderMessage])

        const showNoMessagesPlaceholder = messages.length < 1 || messages.length == 1 && isExternalChatMessageDate(messages[0])
        if (showNoMessagesPlaceholder) {
            return <NoMessagesPlaceholder type={type} contactId={contactId} />
        }

        return (
            <Virtuoso
                tabIndex={undefined} // by default Virtuoso add the tabIndex="0" but for better support SelectionContainer we should set it undefined
                ref={virtuosoRef}
                followOutput
                data={messages}
                itemContent={messageRenderer}
                computeItemKey={generateMessageKey}
                atTopStateChange={onTopStateChange}
                initialTopMostItemIndex={initialScrollPosition}
                customScrollParent={scrollNode || undefined}
                components={virtuosoComponents}
                rangeChanged={setListRange}
                totalListHeightChanged={debouncedOnHeightChange}
                increaseViewportBy={VIEWPORT_INCREASE}
            />
        )
    })

