import type {FC} from 'react'
import React from 'react'

import type {ChatSessionTranscriptMessage} from '@bpinc/ad-interaction-state-types'

import {useMarkAsReadVisibilitySensor} from '../../../../hooks/useMarkAsReadVisibilitySensor'
import type {ChatScrollNodeType} from '../../../../types'
import type {MessageAdditionalProps} from './Message'
import {Message} from './Message'


type ChatMessageProps = MessageAdditionalProps & {
    itemContactId?: string
    itemInteractionStepId?: string
    isDIDChat: boolean
    scrollNode: ChatScrollNodeType
    message: ChatSessionTranscriptMessage
}
export const ChatMessage: FC<ChatMessageProps> = ({
    itemContactId = '',
    itemInteractionStepId = '',
    scrollNode,
    isDIDChat,
    message,
    ...restProps
}) => {
    const ref = useMarkAsReadVisibilitySensor({
        msgId: message.msgId,
        itemInteractionStepId,
        itemContactId,

        scrollNode,
        source: message.chatMessageData.source,
        isRead: message.chatMessageData.isRead,
        isDIDChat,
    })

    return <Message message={message} {...restProps} ref={ref} />
}
