import {useEffect, useState} from 'react'

import {useInteractionChatStateMethods} from '@bpinc/ad-interaction-chat-state-context'


interface UseAgentTypingDetectParams {
    interactionStepId?: string
    isExternalChat?: boolean
}
export function useAgentTypingDetect(dependencies: unknown[], {interactionStepId = '', isExternalChat}: UseAgentTypingDetectParams) {
    const [timerValue, setTimerValue] = useState<number>(0)
    const [isTyping, setTyping] = useState<boolean>(false)
    const [isSending, setSendingValue] = useState<boolean>(false)
    const {sendAgentIsTyping, sendAgentIsNotTyping} = useInteractionChatStateMethods()

    useEffect(() => {
        if (isExternalChat) {
            const interval = setInterval(() => {
                setTimerValue(currentTimerValue => currentTimerValue + 1)
            }, 1000)
            return () => clearInterval(interval)
        }
        //eslint-disable-next-line
    }, [isExternalChat])


    useEffect(() => {
        if (isExternalChat) {
            let timeout: number

            if (!isTyping) {
                setTyping(true)
                setSendingValue(false)
            }
            setTimerValue(0)
            if (!isSending) {
                if (dependencies.filter(Boolean).length) {
                    sendAgentIsTyping(interactionStepId)
                }
                setSendingValue(true)
                timeout = setTimeout(() => {
                    setSendingValue(false)
                }, 5000)
            }

            return () => clearTimeout(timeout)
        }
        //eslint-disable-next-line
    }, [...dependencies, isExternalChat])

    useEffect(() => {
        if (isExternalChat && timerValue > 10 && isTyping) {
            sendAgentIsNotTyping(interactionStepId)
            setTyping(false)
        }
        //eslint-disable-next-line
    }, [isTyping, interactionStepId, sendAgentIsNotTyping, timerValue, isExternalChat])

    function stopTyping() {
        setTyping(false)
        sendAgentIsNotTyping(interactionStepId)
    }

    function empty() {
        return
    }

    return isExternalChat ? stopTyping : empty
}
