import type {RefObject} from 'react'
import {useLayoutEffect} from 'react'
import type {ListRange, VirtuosoHandle} from 'react-virtuoso'

import {useAsRef, usePreviousValueRef, useValueUpdated} from '@bpinc/lib-helpers'

import {findFirstVisibleItemWithOffset, getMessageId, isScrollAtBottom} from '../../../helpers'
import type {ChatCommonMessagesType, ChatScrollNodeType} from '../../../types'


export function useSaveScrollPositionEffect(
    messages: ChatCommonMessagesType[],
    virtuosoRef: RefObject<VirtuosoHandle>,
    rangeRef: RefObject<ListRange | undefined>,
    scrollNode: ChatScrollNodeType,
) {
    const messagesRef = useAsRef(messages)
    const messagesPrevRef = usePreviousValueRef(messages)
    const messagesLengthChanged = useValueUpdated(messages.length)

    useLayoutEffect(() => {
        if (!rangeRef.current ||
            isScrollAtBottom(scrollNode) ||
            getMessageId(messagesRef.current[0]) === getMessageId(messagesPrevRef.current?.[0] || '')
        ) {
            return
        }

        if (messagesLengthChanged) {
            const {offset, id} = findFirstVisibleItemWithOffset(scrollNode, rangeRef.current.startIndex, messagesPrevRef.current || [])
            const index = messagesRef.current.findIndex(m => getMessageId(m) === id)
            virtuosoRef.current?.scrollToIndex({index, offset, align: 'start'})
        }
    }, [messagesLengthChanged, messagesPrevRef, messagesRef, rangeRef, scrollNode, virtuosoRef])
}
