import type {ReactNode} from 'react'
import React, {useCallback} from 'react'

import {useInteraction} from '@bpinc/ad-interaction-state-context'
import type {SelectionData} from '@bpinc/lib-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {WithSelectionPopup} from '@bpinc/lib-ui-elements'

import {useChatScrollContainer, useChatSetHasSelection} from './ChatMessagesContext'
import {useSelectionActions} from './useSelectionActions'
import {WithUnmaskAction} from './WithUnmaskAction'


type WithActionsPopupProps = {
    withMaskAction?: boolean
    interactionId: string
    children: ReactNode
}

export const WithSelectionActions: React.FC<WithActionsPopupProps> = ({
    children,
    withMaskAction,
    interactionId,
}) => {
    const setHasSelection = useChatSetHasSelection()
    const interaction = useInteraction(interactionId)
    assertDefined(interaction)
    const getPopupContent = useSelectionActions(interaction, withMaskAction)
    const onSelectionChange = useCallback(
        (selection?: SelectionData) => {
            setHasSelection(!!selection?.range?.toString())
        },
        [setHasSelection],
    )

    const scrollContainer = useChatScrollContainer()

    return (
        <WithSelectionPopup
            getPopupContent={getPopupContent}
            onSelectionChange={onSelectionChange}
            mountNode={scrollContainer}
            popupContentClassName="border-none bg-transparent shadow-none flex gap-1"
        >
            {withMaskAction ? (
                <WithUnmaskAction interactionId={interactionId}>
                    {children}
                </WithUnmaskAction>
            ) : (
                children
            )}
        </WithSelectionPopup>
    )
}
