import {useCallback, useRef} from 'react'

import {useActivityHistoryByContactId} from '@bpinc/ad-contacts-state-context'
import {chatTypeToMedia} from '@bpinc/ad-formatting-helpers'
import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'
import {useContactActivityHistory} from '@bpinc/ad-interaction-state-hooks'
import {useAsRef} from '@bpinc/lib-helpers'


const MAX_ERRORS_NUMBER_IN_A_ROW = 5

export function useMessagesLoader(
    itemId: string,
    contactId: string,
    type: ChatType,
) {
    const fetchMore = useFetchMoreExternalMessages(itemId, contactId, type)
    const hasActiveRequest = useRef(false)
    const errorsCountRef = useRef(0)

    return useCallback(() => {
        const fetchData = () => {
            if (
                hasActiveRequest.current ||
                errorsCountRef.current > MAX_ERRORS_NUMBER_IN_A_ROW
            ) {
                return Promise.resolve(false)
            }
            hasActiveRequest.current = true

            return fetchMore()
                .then(r => {
                    errorsCountRef.current = 0
                    hasActiveRequest.current = false
                    return r
                }).catch(err => {
                    errorsCountRef.current += 1
                    hasActiveRequest.current = false
                    console.error(err || 'Error while trying load messages again until scroll')
                    fetchData()
                })
        }

        fetchData()
    }, [fetchMore])
}

function useFetchMoreExternalMessages(itemId: string, contactId: string, type: ChatType) {
    const contactActivityHistoryRef = useAsRef(useActivityHistoryByContactId(contactId))
    const getMessagesRef = useAsRef(useContactActivityHistory().getMessages)

    return useCallback(
        function fetchMoreExternalMessages() {
            const {isLoading, reachedLimitByMedia} = contactActivityHistoryRef.current || {}
            if (isLoading || type === 'transcript' || reachedLimitByMedia?.[chatTypeToMedia[type]] || !contactId) {
                return Promise.resolve(false)
            }

            return getMessagesRef.current({contactId, type})
                .then(() => true)
                .catch(err => console.error(err || 'Error while trying loadMessages when top is reached'))
        },
        [contactActivityHistoryRef, contactId, getMessagesRef, type],
    )
}
