import type {FileError, FileRejection} from 'react-dropzone'
import {ErrorCode} from 'react-dropzone'

import type {ActivitiesListItemType, PartyUserShortType} from '@bpinc/ad-activities-state-types'
import {CHAT_PROHIBITED_EXTENSIONS} from '@bpinc/ad-constants'
import type {ChatMessage, MessageDisplayType} from '@bpinc/ad-interaction-message-types'
import type {VoiceTranscriptCommonMessage} from '@bpinc/ad-interaction-state-types'
import {addTime, formatDate, getFileNameAndExtension} from '@bpinc/lib-helpers'
import type {MessagesDeclaration} from '@bpinc/state-localization'

import type {
    ActivityMessageGroup,
    ChatCommonMessagesType,
    ChatScrollNodeType,
    ExternalChatMessageFileData,
    ExternalChatMessageWithDate,
    HistoryChatMessage,
    HistoryFileMessage,
    HistoryMessage,
} from '../types'
import {
    isActivityMessageGroup,
    isChatMessage,
    isExternalChatMessage,
    isHistoryCallMessage,
    isHistoryFileMessageMessage,
    isHistoryMessage,
    isVoiceTranscriptMessage,
} from '../types'


export function isScrollAtBottom(
    scrollNode: ChatScrollNodeType,
) {
    return ((scrollNode?.scrollTop || 0) + (scrollNode?.clientHeight || 0)) >= (scrollNode?.scrollHeight || 0)
}


export function getMessageId(message: ChatCommonMessagesType): string {
    if (isVoiceTranscriptMessage(message)) {
        if (message.chatMessageData?.displayType === 'error') {
            return `${message.timestamp}-error`
        }

        if (message.chatMessageData) {
            return getExternalChatMessageId(message.chatMessageData)
        }

        return getVoiceTranscriptMessageId(message)
    }

    if (isExternalChatMessage(message)) {

        if (isActivityMessageGroup(message)) {
            return getActivitiyMessageGroupMessageId(message)
        }

        const displayType = getExternalChatMessageDisplayType(message)

        if (displayType === 'scenario') {
            return getScenarioMessageId(message)
        }

        if (isHistoryMessage(message)) {
            return geHistoryMessageId(message)
        }


        return getExternalChatMessageId(message)

    }

    return message

    function getActivitiyMessageGroupMessageId(msg: ActivityMessageGroup) {
        return `${msg.timestamp}-${msg.msgId ?? ''}-${msg.partyId}-${
            msg.notes ? 'notes' : 'disposition'
        }`
    }
    function getScenarioMessageId(msg: ChatMessage | HistoryMessage) {
        return `${msg.timestamp}-${msg.msg}-scenario`
    }
    function getVoiceTranscriptMessageId(msg: VoiceTranscriptCommonMessage) {
        return `${msg.partyId}-${msg.msgId}-${msg.timestamp}`
    }

    function getExternalChatMessageId(msg: ChatMessage) {
        return `${msg.timestamp}-${msg.msgId ?? ''}-${
            msg.source
        }`
    }

    function geHistoryMessageId(msg: HistoryMessage) {
        return `${msg.timestamp}-${msg.msgId ?? ''}-${
            msg.direction
        }`
    }
}


export function findFirstVisibleItemWithOffset(
    scrollNode: ChatScrollNodeType,
    startIndex: number,
    messages: ChatCommonMessagesType[],
): {id: string; offset: number} {
    const item = scrollNode?.querySelector<HTMLDivElement>(`[data-index='${startIndex}']`)
    if (!item) {
        return {id: '', offset: 0}
    }
    const offset = (scrollNode?.scrollTop || 0) - item.offsetTop
    if (offset > 0) {
        return findFirstVisibleItemWithOffset(scrollNode, startIndex + 1, messages)
    }
    const message = messages[startIndex]
    if (!message) {
        return {id: '', offset: 0}
    }
    if (typeof message === 'string') {
        return findFirstVisibleItemWithOffset(scrollNode, startIndex + 1, messages)
    }
    const id = getMessageId(messages[startIndex] || '')
    return {id, offset}
}

//TODO: Discuss error messages with the management
export function getFileErrors(t: MessagesDeclaration, file: File) {
    if (!file.name) {
        return null
    }

    const {extension, name} = getFileNameAndExtension(file.name)
    const isBadExtension = CHAT_PROHIBITED_EXTENSIONS.includes(`.${extension}`)

    const errors: FileError[] = []

    if (isBadExtension) {
        errors.push({code: ErrorCode.FileInvalidType, message: t.EXTERNAL_CHAT.invalid_file_extension_error(name)})
    }

    if (!errors.length) {
        return null
    }

    return errors
}

export function getFileRejection(t: MessagesDeclaration, file: File): FileRejection | null {
    const errors = getFileErrors(t, file)

    if (!errors) {
        return null
    }

    return {
        file,
        errors,
    }
}

export function activityToChatMessage(selfUserId: string | undefined) {
    return function activityToChatMessageInner(
        activity: ActivitiesListItemType,
    ): ActivityMessageGroup[] {
        return activity.parties.reduce<ActivityMessageGroup[]>(
            (activityMessages, party) => {
                if (party.partyType === 'USER' && (party.disposition || party.notes)) {
                    const userParty = party as PartyUserShortType
                    activityMessages.push(
                        partyUserShortTypeToActivityMessageGroup(
                            userParty,
                            activity,
                            selfUserId,
                        ),
                    )
                }

                return activityMessages
            },
            [],
        )
    }
}

function partyUserShortTypeToActivityMessageGroup(
    party: PartyUserShortType,
    activity: ActivitiesListItemType,
    selfUserId: string | undefined,
): ActivityMessageGroup {
    const id = activity.Id.oid
    const createdTime = party.startTime?.date || activity.createdTime.date
    const mediaType = activity.mediaType
    const serviceId = party.serviceId
    const service = serviceId ? activity.services?.find(s => s.serviceId === serviceId) : undefined
    return {
        mediaType,
        msgId: id,
        displayType: 'activityGroup' as const,
        notes: party.notes,
        disposition: party.disposition,
        timestamp: createdTime ? addTime(createdTime, 'seconds', party.duration ?? 0).getTime() : 0,
        serviceId: service?.serviceId ?? party.serviceId,
        serviceName: service?.serviceName ?? '',
        firstName: party.firstName,
        lastName: party.lastName,
        userId: party.userId,
        isSelf: party.userId === selfUserId,
        partyId: party.partyId,
    }
}

export function getDispositionTitle(
    {firstName, lastName, timestamp, mediaType}: ActivityMessageGroup,
    t: MessagesDeclaration,
    formatFullName: (first: string, last: string) => string,
) {
    const fullName = formatFullName(firstName, lastName)
    const date = formatDate(timestamp, 'll')
    const time = formatDate(timestamp, 'h:mm A')

    return mediaType === 'CHAT'
        ? t.EXTERNAL_CHAT.disposition_message_title(fullName, date, time)
        : t.VOICE_CALL.disposition_message_title(fullName, date, time)
}

export function getDispositionText(
    message: ActivityMessageGroup,
    t: MessagesDeclaration,
    formatFullName: (first: string, last: string) => string,
) {
    return `${getDispositionTitle(message, t, formatFullName)}\n\n` +
    `${message.disposition}\n\n` +
    `${t.EXTERNAL_CHAT.disposition_message_service_name()} ${message.serviceName}`
}

export function getHistoryOrChatMessageText(
    message: HistoryMessage | ChatMessage,
    t: MessagesDeclaration,
) {
    const fileData = getExternalChatFileData(message)
    if (fileData) {
        return fileData.fileName
    }

    if (isChatMessage(message)) {
        if (message.page) {
            return message.title || t.EXTERNAL_CHAT.external_chat_customer_navigates_to()
        }
        return getHtmlMessageText(message.msg || '')
    }

    if (isHistoryCallMessage(message)) {
        const title = message.isMissedCall
            ? t.ACTIVITY_HISTORY.call_message_missed()
            : message.direction === 'incoming'
                ? t.ACTIVITY_HISTORY.call_message_incoming_text()
                : t.ACTIVITY_HISTORY.call_message_outgoing_text()


        return `${title}\n\n` +
          `${formatDate(message.timestamp, 'LT')}, ${message.duration}\n\n`
    }


    if ('msg' in message && typeof message.msg === 'string') {
        return getHtmlMessageText(message.msg)
    }
}

export function getHtmlMessageText(htmlContent: string) {
    const messageTextElem = document.createElement('div')
    messageTextElem.innerHTML = htmlContent
    messageTextElem.style.opacity = '0'
    messageTextElem.style.position = 'absolute'
    messageTextElem.style.display = 'hidden'
    messageTextElem.style.zIndex = '-1000'

    document.body.append(messageTextElem)
    const elementText = messageTextElem.innerText
    messageTextElem.remove()

    return elementText
}

export function getExternalChatMessageDisplayType(message: ExternalChatMessageWithDate | undefined): MessageDisplayType | undefined {
    if (isChatMessage(message)) {
        return message.displayType
    }
    if (isHistoryMessage(message)) {
        return (message as HistoryFileMessage | HistoryChatMessage).displayType
    }
    if (isActivityMessageGroup(message)) {
        return message.displayType
    }
    return undefined
}

export function getExternalChatMessageDisplayName(
    message: ExternalChatMessageWithDate,
    formatFullName: (first: string | undefined, last: string | undefined) => string,
) {
    const users = isHistoryCallMessage(message) ? message.users : undefined
    if (users?.length) {
        return users.map(user => formatFullName(user.firstName, user.lastName)).join(', ')
    }

    const displayName = isChatMessage(message) ? message.displayName : undefined
    if (displayName) {
        return displayName
    }

    const {firstName, lastName} = isHistoryMessage(message) ? message : {firstName: undefined, lastName: undefined}
    if (firstName || lastName) {
        return formatFullName(firstName, lastName)
    }
}

export function isSystemExternalChatMessage(message: ExternalChatMessageWithDate | undefined): boolean {
    return isChatMessage(message) && message.displayType === 'system'
}

export function isInviteExternalChatMessage(message: ExternalChatMessageWithDate | undefined): boolean {
    return isChatMessage(message) && message.displayType === 'invite'
}

export function isErrorExternalChatMessage(message: ExternalChatMessageWithDate | undefined): boolean {
    return isChatMessage(message) && message.displayType === 'error'
}

export function isLocationExternalChatMessage(message: ExternalChatMessageWithDate | undefined): boolean {
    return isChatMessage(message) && message.displayType === 'location'
}

export function getExternalChatFileData(
    message: ExternalChatMessageWithDate | undefined,
): ExternalChatMessageFileData | undefined {
    if (isChatMessage(message) && message.displayType === 'file') {
        return {
            fileId: message.fileId,
            fileError: message.fileError,
            fileIsMasked: message.fileIsMasked,
            fileProgress: message.fileProgress,
            fileName: message.fileName,
            fileType: message.fileType,
            refMsgId: undefined,
        }
    }
    if (isHistoryFileMessageMessage(message)) {
        return {
            fileId: message.fileId,
            refMsgId: message.refMsgId,
            fileType: message.fileType,
            fileName: message.fileName,
            fileError: undefined,
            fileIsMasked: undefined,
            fileProgress: undefined,
        }
    }
}
