import type {FC} from 'react'
import React from 'react'

import {useGetContactPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import {formatPhone} from '@bpinc/ad-formatting-helpers'
import {AvatarLegacy} from '@bpinc/lib-avatar-ui'


interface ContactInfoProps {
    firstName: string | undefined
    lastName: string | undefined
    fullName: string | undefined
    contactId: string | undefined
    phoneNumber?: string | undefined
}

export const ContactInfo: FC<ContactInfoProps> = ({firstName, lastName, fullName, contactId, phoneNumber}) => {

    const getContactPictureUrl = useGetContactPictureUrl()

    return (
        <>
            <AvatarLegacy
                firstName={firstName}
                lastName={lastName}
                type="contact"
                size={44}
                url={getContactPictureUrl(contactId)}
            />
            <div className="ml-4">
                <div className="text-xl leading-tight">{fullName}</div>
                {phoneNumber && (
                    <div className="self-end opacity-80">
                        {formatPhone(phoneNumber)}
                    </div>
                )}
            </div>
        </>
    )
}
