import type {ReactElement} from 'react'
import React, {useMemo, useState} from 'react'
import {createContext, useContext} from 'use-context-selector'

import type {Toggle} from '@bpinc/lib-helpers'
import {useBoolean} from '@bpinc/lib-helpers'
import type {FCWithChildren, StateSetter} from '@bpinc/lib-types-helpers'

import type {VoiceTranscriptSearchResultType} from '../../../types'


interface VoiceTranscriptUIState {
    search: string
    setSearch: StateSetter<string>

    searchResult: VoiceTranscriptSearchResultType | undefined
    setSearchResult: StateSetter<VoiceTranscriptSearchResultType | undefined>

    showBotSuggestions: boolean
    toggleBotSuggestions: Toggle

    showSMS: boolean
    toggleSMS: Toggle

    showTranscript: boolean
    toggleTranscript: Toggle
}

const noop = () => {}

const initialState: VoiceTranscriptUIState = {
    search: '',
    setSearch: noop,

    searchResult: undefined,
    setSearchResult: noop,

    showBotSuggestions: true,
    toggleBotSuggestions: noop,

    showSMS: true,
    toggleSMS: noop,

    showTranscript: true,
    toggleTranscript: noop,
}

const VoiceTranscriptUIContext = createContext<VoiceTranscriptUIState>(initialState)

VoiceTranscriptUIContext.displayName = 'VoiceTranscriptUIContext'

export const VoiceTranscriptStateProvider: FCWithChildren<ReactElement> = ({children}) => {
    const [search, setSearch] = useState<VoiceTranscriptUIState['search']>(initialState.search)
    const [searchResult, setSearchResult] = useState<VoiceTranscriptUIState['searchResult']>(undefined)
    const {value: showBotSuggestions, toggle: toggleBotSuggestions} = useBoolean(initialState.showBotSuggestions)
    const {value: showSMS, toggle: toggleSMS} = useBoolean(initialState.showSMS)
    const {value: showTranscript, toggle: toggleTranscript} = useBoolean(initialState.showTranscript)

    const contextValue = useMemo(function memoizeContextValue() {
        return {
            search,
            setSearch,

            searchResult,
            setSearchResult,

            showBotSuggestions,
            toggleBotSuggestions,

            showSMS,
            toggleSMS,

            showTranscript,
            toggleTranscript,
        }
    }, [search, searchResult, showBotSuggestions, showSMS, showTranscript, toggleBotSuggestions, toggleSMS, toggleTranscript])

    return <VoiceTranscriptUIContext.Provider value={contextValue}>{children}</VoiceTranscriptUIContext.Provider>
}

export function useVoiceTranscriptUIState() {
    return useContext(VoiceTranscriptUIContext)
}
