import React, {useMemo} from 'react'

import {useGetUserPictureUrl} from '@bpinc/ad-contacts-state-hooks'
import {Avatar} from '@bpinc/lib-avatar-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import styles from '../../../../styles/Chat.module.less'


interface ExternalMessageAvatar {
    userId?: string
    displayName?: string
    isSelf?: boolean
    isScenarioAvatar?: boolean
    showUserAvatar?: boolean
    className?: string
    isConference?: boolean
}

const avatarClassName = 'm-0 mr-1.5'

export const MessageAvatar: React.FC<ExternalMessageAvatar> = ({
    userId,
    displayName,
    isSelf,
    isScenarioAvatar,
    className,
    isConference,
    showUserAvatar = true,
}) => {
    const getUserPictureUrl = useGetUserPictureUrl()

    const avatarUrl = useMemo(() => {
        if (isConference) {
            return undefined
        }
        return !isSelf && userId ? getUserPictureUrl(userId) : ''
    }, [getUserPictureUrl, isConference, isSelf, userId])

    const [firstName, lastName] = useMemo(() => {
        return displayName ? displayName.split(' ') : ['', '']
    }, [displayName])

    if (isScenarioAvatar) {
        return (
            <Avatar
                className={tcn(className, styles.avatar, avatarClassName)}
                type="scenario"
                size="28"
            />
        )
    }

    return (
        <>
            {showUserAvatar && (
                <Avatar
                    className={tcn(className, styles.avatar, avatarClassName)}
                    url={avatarUrl}
                    size="28"
                    firstName={firstName}
                    lastName={lastName}
                    type={isConference ? 'conference' : 'contact'}
                />
            )}
        </>
    )
}
