import {useCallback} from 'react'
import {flow} from 'lodash-es'

import {Selection} from '@bpinc/lib-helpers'


export function useMessageUpdater(
    setCurrentMessage: (setter: (message: string) => string) => void,
    textAreaSelector: string,
) {
    return useCallback(
        function updateMessageCallback<T, S extends string>(getMessage = (e: T) => e as unknown as S) {
            return function updater(data: T) {
                const textAreaNode = document.querySelector(textAreaSelector)

                flow(getMessage, updateMessageContent, setCaretToRightPosition)(data)


                function updateMessageContent(dataToAdd: string) {
                    let offsetToFocus = 0
                    let nodeToFocus: Node | null = null

                    setCurrentMessage((currentMessage: string) => {
                        const selection = new Selection()
                        const selectionNode = selection?.getLastNode()
                        const selectionElement = (
                            selectionNode?.nodeType === Node.TEXT_NODE ? selectionNode?.parentElement : selectionNode
                        ) as Element

                        const textAreaSelected = selectionElement?.closest(textAreaSelector)

                        if (textAreaNode) {
                            currentMessage = cutExtraBR()

                            if (!textAreaSelected) {
                                return currentMessage + dataToAdd
                            } else if (selection && selectionNode) {
                                setNodeAndOffsetToFocus(selection)
                                removeSelectedTextOnPaste(selection)

                                if (nodeToFocus) {
                                    insertDataInSelectedNode(nodeToFocus, offsetToFocus)
                                }

                                return getUpdatedHTMLAsString()
                            }

                            return currentMessage
                        }

                        return currentMessage + dataToAdd

                        function differentNodesSelected(selectionInner: Selection) {
                            return selectionInner.getFirstNode() !== selectionInner.getLastNode()
                        }

                        function textSelected(selectionInner: Selection) {
                            return selectionInner.getSelectionStartIndex() !== selectionInner.getSelectionEndIndex()
                        }

                        function setNodeAndOffsetToFocus(selectionInner: Selection) {
                            if (differentNodesSelected(selectionInner)) {
                                offsetToFocus = selectionInner.getSelectionStartIndex() ?? 0
                                nodeToFocus = selectionInner.getFirstNode()
                            } else if (textSelected(selectionInner)) {
                                offsetToFocus = selectionInner.getSelectionStartIndex() ?? 0
                                nodeToFocus = selectionInner.getFirstNode()
                            } else {
                                offsetToFocus = selectionInner.getSelectionEndIndex() ?? 0
                                nodeToFocus = selectionInner.getLastNode()
                            }
                        }
                        function removeSelectedTextOnPaste(selectionInner: Selection) {
                            if (differentNodesSelected(selectionInner) || textSelected(selectionInner)) {
                                selectionInner.deleteSelected()
                            }
                        }

                        function insertDataInSelectedNode(selectionNodeInner: Node, selectionOffset: number) {
                            const textContent = selectionNodeInner?.textContent ?? ''
                            selectionNodeInner.textContent =
                                textContent?.slice(0, selectionOffset) + dataToAdd + textContent?.slice(selectionOffset)
                        }

                        function getUpdatedHTMLAsString() {
                            return document.querySelector(textAreaSelector)?.innerHTML ?? currentMessage
                        }

                        function cutExtraBR() {
                            const extraBRExist = currentMessage.substring(currentMessage.length - 8) === '<br><br>'
                            if (extraBRExist) {
                                return currentMessage.substring(0, currentMessage.length - 4)
                            } else {
                                return currentMessage
                            }
                        }
                    })

                    return {nodeToFocus, offsetToFocus, dataToAdd} as const
                }

                function setCaretToRightPosition({
                    nodeToFocus,
                    offsetToFocus,
                    dataToAdd,
                }: {
                    nodeToFocus: Node | null
                    offsetToFocus: number
                    dataToAdd: string
                }) {
                    if (textAreaNode instanceof HTMLElement) {
                        textAreaNode.focus()
                        requestAnimationFrame(() => {
                            setCursorPosition()
                            requestAnimationFrame(setCursorPosition)

                            function setCursorPosition() {
                                if (textAreaNode) {
                                    if (nodeToFocus) {
                                        keepCursorAtTheCurrentPosition(nodeToFocus)
                                    } else if (textAreaNode.lastChild) {
                                        setCursorToTheEnd(textAreaNode.lastChild)
                                    }
                                }
                            }

                            function keepCursorAtTheCurrentPosition(node: Node) {
                                const range = document.createRange()
                                range.setStart(node, offsetToFocus + dataToAdd.length)
                                range.setEnd(node, offsetToFocus + dataToAdd.length)
                                window.getSelection()?.removeAllRanges()
                                window.getSelection()?.addRange(range)
                            }

                            function setCursorToTheEnd(lastChild: Node) {
                                const range = document.createRange()
                                range.setStart(lastChild, lastChild.textContent?.length ?? 0)
                                range.setEnd(lastChild, lastChild.textContent?.length ?? 0)
                                window.getSelection()?.removeAllRanges()
                                window.getSelection()?.addRange(range)
                            }
                        })
                    }
                }
            }
        },
        [textAreaSelector, setCurrentMessage],
    )
}
