import type {MutableRefObject, RefObject} from 'react'
import React, {useEffect, useRef} from 'react'
import type {ListRange} from 'react-virtuoso'

import {useInteractionChatData} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId, getTypingParties} from '@bpinc/ad-interaction-state-methods'
import {useAsRef} from '@bpinc/lib-helpers'

import type {ChatCommonMessagesType} from '../../../../types'
import {TypingIndicator} from './TypingIndicator'


type TypingPartiesProps = {
    itemId: string
    rangeRef: RefObject<ListRange | undefined>
    messagesRef: MutableRefObject<ChatCommonMessagesType[]>
}

export const TypingParties: React.FC<TypingPartiesProps> = ({itemId, rangeRef, messagesRef}) => {
    const bottom = useRef<HTMLDivElement>(null)
    const interactionStepId = useInteractionProp(itemId, getInteractionStepId)
    const chatData = useInteractionChatData(interactionStepId)
    const typingParties = getTypingParties(chatData)
    const hasTypingParties = typingParties.length > 0
    const lastRenderedItem = rangeRef.current?.endIndex
    const lastMessageIndex = messagesRef.current.length - 1
    const isLastMessageRenderedRef = useAsRef(lastMessageIndex === lastRenderedItem)

    useEffect(() => {
        if (!hasTypingParties) {
            return
        }

        if (!isLastMessageRenderedRef.current) {
            return
        }

        bottom.current?.scrollIntoView({behavior: 'auto', block: 'start'})
    }, [hasTypingParties, isLastMessageRenderedRef])

    return (
        <>
            {typingParties.map((party, i) => (
                <TypingIndicator
                    key={i}
                    party={party}
                    typingParty={chatData?.typingParties[party.party_id]}
                />
            ))}
            {hasTypingParties && <div ref={bottom} />}
        </>
    )
}
