import {useMemo} from 'react'
import {orderBy} from 'lodash-es'

import type {ChatMessage as ChatMessageType, VoiceTranscriptMessageType} from '@bpinc/ad-interaction-message-types'
import type {VoiceTranscriptCommonMessage, VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'

import {chatMessageToVoiceTranscript} from './utils'
import {useVoiceTranscriptUIState} from './VoiceTranscriptContext'


type UseVoiceTranscriptVisibleMessagesParams = {
    voiceTranscript: VoiceTranscriptCommonMessage[]
    botSuggestions: VoiceTranscriptSuggestion[]
    chatMessages: ChatMessageType[]
}
export function useVoiceTranscriptVisibleMessages({chatMessages, voiceTranscript, botSuggestions}: UseVoiceTranscriptVisibleMessagesParams) {
    const {showBotSuggestions, showTranscript, showSMS} = useVoiceTranscriptUIState()

    const visibleMessages = useMemo(
        function memoizeVisibleMessages() {
            const allMessages = showSMS
                ? orderBy([...voiceTranscript, ...botSuggestions, ...getSMSMessagesForTranscript()], 'endTime')
                : [...voiceTranscript]

            return allMessages.filter(m => {
                return filterByType(m.type)
            })

            function filterByType(type: VoiceTranscriptMessageType) {
                const allowType = {
                    voiceTranscriptMessage: showTranscript,
                    suggestion: showBotSuggestions,
                    chatMessage: showSMS,
                }

                return allowType[type]
            }

            function getSMSMessagesForTranscript(): VoiceTranscriptCommonMessage[] {
                return chatMessages.filter(message => message.displayType === 'message' || message.displayType === 'error').map(chatMessageToVoiceTranscript)
            }
        },


        [botSuggestions, chatMessages, showBotSuggestions, showSMS, showTranscript, voiceTranscript],
    )

    return visibleMessages
}
