import {useMemo} from 'react'
import {get} from 'lodash-es'

import {useContactsState} from '@bpinc/ad-contacts-state-context'


export function useExternalActivityHistoryLoaded(contactId: string) {
    const activityHistory = useContactsState('activityHistory')

    return useMemo(
        () => get(activityHistory[contactId], 'reachedLimit', true),
        [activityHistory, contactId],
    )
}
