import {useMemo} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import {useChatAttachmentsUIState} from '@bpinc/lib-attachments-state'


export function useAttachmentsList(itemId: string) {
    const interactionStepId = useInteractionProp(itemId, getInteractionStepId)
    const getAttachmentsList = useChatAttachmentsUIState('getAttachmentsList')

    return useMemo(function memoizeAttachmentsList() {
        return getAttachmentsList(interactionStepId)
    }, [getAttachmentsList, interactionStepId])

}
