import type {ReactElement, ReactFragment} from 'react'
import React, {useContext} from 'react'

import {throwNoProviderError} from '@bpinc/lib-helpers'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'

import {initialOption, useChannel} from './state'
import type {ChannelState} from './types'


const contextName = 'ChannelStateContext'
const initialState: ChannelState = {
    getSelectedChannel: () => initialOption,
    getSelectedPhone: () => undefined,
    isTouched: () => false,
    channelOptions: [initialOption],
    setIsTouched: throwNoProviderError(contextName),
    setSelectedChannel: throwNoProviderError(contextName),
}

const ChannelStateContext = React.createContext<ChannelState>(initialState)
ChannelStateContext.displayName = contextName

export const ChannelStateProvider: FCWithChildren<ReactElement | ReactFragment> = props => {
    const channelState: ChannelState = useChannel()

    return (
        <ChannelStateContext.Provider value={channelState}>
            {props.children}
        </ChannelStateContext.Provider>
    )
}

export const useChannelState = () => useContext(ChannelStateContext)
