import type {FC} from 'react'
import React, {useCallback, useMemo, useState} from 'react'

import type {ChatMessage as ChatMessageType} from '@bpinc/ad-interaction-message-types'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {getContactId, getInteractionStepId} from '@bpinc/ad-interaction-state-methods'
import type {VoiceTranscriptCommonMessage, VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'
import {useAsRef} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SelectionContainer} from '@bpinc/lib-ui-elements'
import {useInteractionUIChatScrollerApi} from '@bpinc/state-interaction-ui'

import {getMessageId} from '../../../helpers'
import type {ChatScrollNodeType} from '../../../types'
import {ChatMessagesSelectionActions} from '../ChatMessagesActions'
import {Filters} from './Filters'
import {useVoiceTranscriptMessagesContentMap} from './useVoiceTranscriptMessagesContentMap'
import {useVoiceTranscriptVisibleMessages} from './useVoiceTranscriptVisibleMessages'
import {VirtualizedVoiceTranscriptMessages} from './VirtualizedVoiceTranscriptMessages'


interface VoiceTranscriptProps {
    voiceTranscript: VoiceTranscriptCommonMessage[] | undefined
    botSuggestions: VoiceTranscriptSuggestion[] | undefined
    chatMessages: ChatMessageType[] | undefined
    itemId: string
    className?: string
    filtersClassName?: string
}

const emptyChatMessages: ChatMessageType[] = []
const emptyTranscript: VoiceTranscriptCommonMessage[] = []
const emptySuggestions: VoiceTranscriptSuggestion[] = []

export const VoiceTranscript: FC<VoiceTranscriptProps> = ({
    chatMessages = emptyChatMessages,
    voiceTranscript = emptyTranscript,
    botSuggestions = emptySuggestions,
    itemId,
    className,
    filtersClassName,
}) => {
    const scrollerApi = useInteractionUIChatScrollerApi(itemId)
    const [scrollNode, setScrollNode] = useState<ChatScrollNodeType>(null)
    const interaction = useInteraction(itemId)
    const contactId = getContactId(interaction)
    const interactionStepId = getInteractionStepId(interaction)

    const visibleMessages = useVoiceTranscriptVisibleMessages({voiceTranscript, chatMessages, botSuggestions})
    const messagesContent = useVoiceTranscriptMessagesContentMap(visibleMessages, itemId)

    const itemsIds = useMemo(() => visibleMessages.map(getMessageId), [visibleMessages])
    const itemsIdsRef = useAsRef(itemsIds)
    const itemsProvider = useCallback(() => itemsIdsRef.current, [itemsIdsRef])
    const onItemSelect = useCallback((id: string) => {
        scrollerApi?.scrollTo(id, 'center')
    }, [scrollerApi])

    return (
        <div className={tcn('flex h-full flex-col', className)}>
            <Filters className={filtersClassName} messagesContent={messagesContent} itemId={itemId} />
            <SelectionContainer
                startFrom="bottom"
                className="mt-1 flex h-0 flex-auto flex-col"
                itemsListProvider={itemsProvider}
                onItemSelect={onItemSelect}
            >
                <div
                    className="relative mr-1.5 flex-auto overflow-y-auto overflow-x-hidden pl-4 pr-2"
                    ref={setScrollNode}
                >
                    <ChatMessagesSelectionActions messages={visibleMessages} interactionId={itemId} scrollContainer={scrollNode}>
                        <VirtualizedVoiceTranscriptMessages
                            itemId={itemId}
                            contactId={contactId}
                            messages={visibleMessages}
                            interactionStepId={interactionStepId}
                            scrollNode={scrollNode}
                            messagesContent={messagesContent}
                        />
                    </ChatMessagesSelectionActions>
                </div>
            </SelectionContainer>
        </div>
    )
}

