import React, {cloneElement, isValidElement, useMemo} from 'react'

import {SmMessage, SmTransition} from '@bpinc/lib-external-ui-elements'
import {useNotificationsState} from '@bpinc/lib-notifications-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {truthy} from '@bpinc/lib-types-helpers'
import {AriaStatus} from '@bpinc/lib-ui-elements'

import styles from './Notifications.module.less'


const LIMIT = 3

type Props = {
    omit?: string[]
    show?: string[]
    className?: string
}

const duration = {hide: 0, show: 300}

// TODO: remove <ServiceSelect /> from this component, move it to prop or content
export const Notifications: React.FC<Props> = ({omit, show, className}) => {
    const {
        notifications: notifications,
        methods: {hideNotificationById},
    } = useNotificationsState()

    const notificationsToRender = useMemo(() => {
        let filteredList = truthy(Object.values(notifications)).filter(n => !n.delayed)

        if (show) {
            filteredList = filteredList.filter(notification => {
                return show.includes(notification.id)
            })
        }

        if (omit) {
            filteredList = filteredList.filter(notification => {
                return !omit.includes(notification.id)
            })
        }

        return filteredList.slice(0, LIMIT)
    }, [notifications, omit, show])

    return (
        <div className={tcn(className, styles.listWrapper)}>
            <SmTransition.Group animation="fade down" duration={duration}>
                {notificationsToRender.map(notification => {
                    let content: React.ReactNode

                    if (notification.content) {
                        content = isValidElement(notification.content)
                            ? cloneElement(notification.content, {onHide: () => hideNotificationById(notification.id)})
                            : notification.content
                    } else {
                        content = notification.message
                    }

                    return (
                        <div key={notification.id} className={styles.listItem}>
                            <div>
                                <SmMessage
                                    onDismiss={
                                        notification.closable
                                            ? () => hideNotificationById(notification.id)
                                            : undefined
                                    }
                                    content={content}
                                    error={notification.type === 'error'}
                                    info={notification.type === 'info'}
                                    warning={notification.type === 'warning'}
                                    success={notification.type === 'success'}
                                />
                            </div>
                            <AriaStatus>{content}</AriaStatus>
                        </div>
                    )
                })}
            </SmTransition.Group>
        </div>
    )
}
