import React, {memo} from 'react'

import type {KBFeatures} from '@bpinc/kb-types'
import {createComponentLoader} from '@bpinc/lib-ui-elements'
import {assertKBBridgeInterface, useKbBridgeConnector} from '@bpinc/state-kb-bridge-provider'


const {KBPageLoader} = createComponentLoader('knowledgeBase', import('knowledgeBase'), 'KBPage')

const features: Partial<KBFeatures> = {
    topHeader: true,
    openInNewTab: true,
}

export const VoiceTranscriptKB: React.FC = memo(function VoiceTranscriptKB() {
    const kbBridge = useKbBridgeConnector()

    assertKBBridgeInterface(kbBridge)

    return <KBPageLoader features={features} kbBridge={kbBridge} />
})
