import React from 'react'

import {useFormatting} from '@bpinc/ad-formatting-context'
import type {ExternalChatParty, TypingParty} from '@bpinc/ad-interaction-chat-state-types'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {WithOptionalNameAndAvatar} from '../../ExternalMessages/messages/WithOptionalNameAndAvatar'
import {DotsTransforming} from './DotsTransforming'


interface Props {
    party: ExternalChatParty
    typingParty: TypingParty
}

function removeLastThreeDots(str: string): string {
    return str.replace(/\.\.\.$/, '')
}
export const TypingIndicator: React.FC<Props> = ({party, typingParty}) => {
    const {first_name: firstName, last_name: lastName, user_id: userId} = party

    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()
    const name = formatFullName(firstName, lastName)
    const displayName = name || t.EXTERNAL_CHAT.display_name_placeholder()

    const content = removeLastThreeDots(
        userId
            ? t.EXTERNAL_CHAT.agent_is_typing(name)
            : t.EXTERNAL_CHAT.party_is_typing(),
    )

    const timestamp = party?.timestamp
        ? Number(party.timestamp)
        : Date.now().valueOf()

    return (
        <WithOptionalNameAndAvatar
            message={{
                itemId: party.item_id,
                partyId: party.party_id,
                timestamp,
                type: 'message',
                displayType: 'message',
                displayName,
            }}
        >
            {content}
            {' '}
            {typingParty.message}
            <DotsTransforming />
        </WithOptionalNameAndAvatar>
    )
}
