import React, {useEffect} from 'react'

import {useBoolean, useEscapeManager} from '@bpinc/lib-helpers'
import {useInteractionId} from '@bpinc/lib-interaction-context'
import {CircleButton, IconArrowDown8, TriggerDropdown} from '@bpinc/lib-jskit-ui'
import {getChannelImageIcon, PhoneOption} from '@bpinc/lib-ui-elements'

import {useChannelState} from '../../../channel-context'
import {useInitializeDefaultChannelEffect} from './useInitializeDefaultChannelEffect'


interface ChannelSelectorProps {
    disabled?: boolean
}

export const ChannelSelector: React.FC<ChannelSelectorProps> = React.memo(
    function ChannelSelector({disabled}) {
        const itemId = useInteractionId()

        useInitializeDefaultChannelEffect()

        const {channelOptions, getSelectedChannel, getSelectedPhone, setSelectedChannel} =
            useChannelState()

        const selectedPhone = getSelectedPhone(itemId)
        const selectedChannel = getSelectedChannel(itemId)
        /*
         * Use effect that ensures a valid channel is selected when the channel options change.
         * If no channel is currently selected or the selected channel is not present in the updated
         * options, then the first option is selected as the new current channel.
         */
        useEffect(
            function selectFirstValidChannel() {
                if (
                    channelOptions.length &&
                    (!selectedChannel?.value ||
                        channelOptions.every(
                            o => o.value !== selectedChannel.value,
                        ))
                ) {
                    setSelectedChannel(itemId, channelOptions[0])
                }
            },
            [channelOptions, itemId, selectedChannel?.value, setSelectedChannel],
        )


        const show = useBoolean()

        useEscapeManager(show.setFalse, {enabled: show.value})

        if (!channelOptions.length) {
            return null
        }

        const SelectedIcon = getChannelImageIcon(selectedChannel?.value)
        const finalDisabled =
            disabled ||
            (channelOptions.length < 2 &&
                (channelOptions[0]?.phones?.length ?? 0) < 2)

        return (
            <TriggerDropdown
                open={show.value}
                onOpenChange={show.toggle}
                disabled={finalDisabled}
                className="min-w-[168px]"
                modal={false}
                side="top"
                onEscapeKeyDown={e => {
                    e.stopPropagation()
                }}
                align="start"
                data-testid="channel-selector"
                options={
                    <>
                        {channelOptions.map(channel => {
                            function renderChannel() {
                                const Icon = getChannelImageIcon(channel.value)
                                return (
                                    <>
                                        {Icon && (
                                            <Icon className="icon-neutral" />
                                        )}
                                        {channel.text}
                                    </>
                                )
                            }

                            return channel.phones ? (
                                <TriggerDropdown.Sub key={channel.value}>
                                    <TriggerDropdown.SubTrigger contentClassName="flex items-center gap-2">
                                        {renderChannel()}
                                    </TriggerDropdown.SubTrigger>
                                    <TriggerDropdown.SubContent>
                                        {channel.phones.map(phone => (
                                            <TriggerDropdown.Item
                                                asChild={false}
                                                data-selected={phone.id === selectedPhone?.id}
                                                aria-selected={phone.id === selectedPhone?.id}
                                                onClick={() => {
                                                    setSelectedChannel(
                                                        itemId,
                                                        channel,
                                                        phone.id,
                                                    )
                                                }}
                                                key={phone.id}
                                            >
                                                <PhoneOption
                                                    value={phone.phone}
                                                    type={phone.type}
                                                />
                                            </TriggerDropdown.Item>
                                        ))}
                                    </TriggerDropdown.SubContent>
                                </TriggerDropdown.Sub>
                            ) : (
                                <TriggerDropdown.Item
                                    onClick={() => {
                                        setSelectedChannel(itemId, channel)
                                    }}
                                    key={channel.value}
                                    asChild={false}
                                    contentClassName="flex gap-2"
                                >
                                    {renderChannel()}
                                </TriggerDropdown.Item>
                            )
                        })}
                    </>
                }
            >
                <CircleButton className="group flex w-auto shrink-0 gap-0.5 p-[7.5px]">
                    {SelectedIcon && <SelectedIcon />}
                    {!finalDisabled && (
                        <IconArrowDown8 className="group-data-[state=open]:rotate-180" />
                    )}
                </CircleButton>
            </TriggerDropdown>
        )
    },
)
