import type {ReactElement} from 'react'
import React, {
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react'
import {noop} from 'lodash-es'
import {createContext, useContextSelector} from 'use-context-selector'

import {useEvent, useEventListener, useValueUpdated} from '@bpinc/lib-helpers'
import type {StateSetter} from '@bpinc/lib-types-helpers'

import {getMessageId} from '../../../helpers'
import type {ChatCommonMessagesType, ChatScrollNodeType} from '../../../types'


type ChatMessagesContextType = {
    hasSelection: boolean
    setHasSelection: StateSetter<boolean>
    messages: ChatCommonMessagesType[]
    selectedMessages: ChatCommonMessagesType[]
    getClipboardBuffer: () => string
    setClipboardBuffer: StateSetter<string>
    scrollContainer: ChatScrollNodeType
}

const ChatMessagesContext = createContext<ChatMessagesContextType>({
    hasSelection: false,
    setHasSelection: noop,
    messages: [],
    selectedMessages: [],
    getClipboardBuffer: () => '',
    setClipboardBuffer: noop,
    scrollContainer: null,
})
ChatMessagesContext.displayName = 'ChatMessagesContextProvider'

interface ChatMessagesContextProviderProps {
    messages: ChatCommonMessagesType[]
    children: ReactElement
    interactionId: string
    scrollContainer: ChatScrollNodeType
}


export const ChatMessagesContextProvider: React.FC<ChatMessagesContextProviderProps> = ({children, messages, interactionId, scrollContainer}) => {
    const [hasSelection, setHasSelection] = useState(false)
    const [clipboardBuffer, setClipboardBuffer] = useState('')
    const [selectedMessages, setSelectedMessages] = useState<ChatCommonMessagesType[]>([])
    const firstSelectedMessageRef = useRef<string>()
    const getClipboardBuffer = useEvent(() => clipboardBuffer)
    const interactionChanged = useValueUpdated(interactionId)

    useEffect(() => {
        if (!interactionChanged) {
            return
        }

        setHasSelection(false)
        setClipboardBuffer('')
        setSelectedMessages([])
    }, [interactionChanged])

    useEventListener('mouseup', e => {
        if (!firstSelectedMessageRef.current || !(e instanceof MouseEvent)) {
            return
        }

        const lastSelectedMessageId = getNearestMessageId(e.target)
        if (!lastSelectedMessageId) {
            return
        }

        const firstSelectedMessageIndex = messages.findIndex(m => getMessageId(m) === firstSelectedMessageRef.current)
        const lastSelectedMessageIndex = messages.findIndex(m => getMessageId(m) === lastSelectedMessageId)
        const indexes = [firstSelectedMessageIndex, lastSelectedMessageIndex].sort((a, b) => a - b)

        const selected = messages.slice(indexes[0], indexes[1] + 1)
        const selection = window.getSelection()
        const range = (selection?.rangeCount ?? 0) > 0 ? selection?.getRangeAt(0) : undefined

        // If selected more than one message extend selection to include whole messages
        if (selected.length > 1 && selection && range) {
            const forwardSelection = firstSelectedMessageIndex < lastSelectedMessageIndex
            const firstElement = document.querySelector(`[data-message-id-selection='${firstSelectedMessageRef.current}']`)
            const lastElement = document.querySelector(`[data-message-id-selection='${lastSelectedMessageId}']`)
            const startElement = forwardSelection ? firstElement : lastElement
            const endElement = forwardSelection ? lastElement : firstElement
            range.setStartBefore(startElement || range.startContainer)
            range.setEndAfter(endElement || range.endContainer)
            if (forwardSelection) {
                selection.setBaseAndExtent(range.startContainer, range.startOffset, range.endContainer, range.endOffset)
            } else {
                selection.setBaseAndExtent(range.endContainer, range.endOffset, range.startContainer, range.startOffset)
            }
        }
        setSelectedMessages(selected)
    })

    const mouseDownHandler = useCallback((e: React.MouseEvent<HTMLDivElement>) => {
        firstSelectedMessageRef.current = getNearestMessageId(e.target)
    }, [])

    const value = useMemo<ChatMessagesContextType>(
        () => ({
            hasSelection,
            setHasSelection,
            messages,
            selectedMessages,
            setClipboardBuffer,
            getClipboardBuffer,
            scrollContainer,
        }),
        [getClipboardBuffer, hasSelection, messages, scrollContainer, selectedMessages],
    )

    return (
        <ChatMessagesContext.Provider value={value}>
            <div onMouseDown={mouseDownHandler}>
                {children}
            </div>
        </ChatMessagesContext.Provider>
    )
}

export const useChatHasSelection = () => useContextSelector(ChatMessagesContext, data => data.hasSelection)
export const useChatSetHasSelection = () => useContextSelector(ChatMessagesContext, data => data.setHasSelection)
export const useChatMessages = () => useContextSelector(ChatMessagesContext, data => data.messages)
export const useChatSelectedMessages = () => useContextSelector(ChatMessagesContext, data => data.selectedMessages)
export const useChatGetClipboardBuffer = () => useContextSelector(ChatMessagesContext, data => data.getClipboardBuffer)
export const useChatSetClipboardBuffer = () => useContextSelector(ChatMessagesContext, data => data.setClipboardBuffer)
export const useChatScrollContainer = () => useContextSelector(ChatMessagesContext, data => data.scrollContainer)


function getNearestMessageId(target: EventTarget | null) {
    let element: Element | undefined = undefined
    if (target instanceof HTMLElement) {
        element = target.closest('[data-message]')?.querySelector('[data-message-id-selection]') || undefined
    } else if (target instanceof Node) {
        element = target.parentElement?.closest('[data-message]')?.querySelector('[data-message-id-selection]') || undefined
    }

    return element?.getAttribute('data-message-id-selection') || undefined
}
