import React, {useCallback} from 'react'

import type {ChatType} from '@bpinc/ad-interaction-chat-state-types'
import {useContactActivityHistory} from '@bpinc/ad-interaction-state-hooks'
import {Button} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useExternalActivityHistoryLoaded} from './useExternalActivityHistoryLoaded'

import styles from './NoMessages.module.less'


interface NoMessagesProps {
    type: ChatType
    contactId: string
}

export const NoMessages = ({type, contactId}: NoMessagesProps) => {
    const {isLoading: activityHistoryIsLoading, getMessages} = useContactActivityHistory()
    const {t} = useLocalizationState()

    const allActivityHistoryLoaded = useExternalActivityHistoryLoaded(contactId)

    const searchForOlder = useCallback(
        function searchForOlderCallback() {
            getMessages({type, contactId})
        },
        [contactId, getMessages, type],
    )

    return (
        <>
            {!activityHistoryIsLoading && allActivityHistoryLoaded ? (
                <div className={styles.noMessages}>
                    <p className={styles.noRecent}>{t.INTERNAL_CHAT.chat_history_no_recent_found(type)}</p>
                </div>
            ) : (
                <div className={styles.noMessages}>
                    <p className={styles.noRecent}>{t.INTERNAL_CHAT.chat_history_no_recent_while_loading(type)}</p>
                    <Button className={styles.searchForOlder} onClick={searchForOlder}>
                        {t.INTERNAL_CHAT.chat_history_search_for_older(type)}
                    </Button>
                </div>
            )}
        </>
    )
}
