import type {VoiceTranscriptCommonMessage} from '@bpinc/ad-interaction-state-types'
import {isCurrentChatTranscriptMessage, isSuggestion} from '@bpinc/ad-interaction-state-types'

import botSuggestionIcon from './images/bot-suggestion.svg'
import facebookIcon from './images/facebook.svg'
import lineIcon from './images/line.svg'
import smsIcon from './images/sms.svg'
import telegramIcon from './images/telegram.svg'
import twitterIcon from './images/twitter.svg'
import viberIcon from './images/viber.svg'
import webChatIcon from './images/web-chat.svg'
import wechatIcon from './images/wechat.svg'
import whatsappIcon from './images/whatsapp.svg'


export function getTranscriptMessageIcon(transcriptMessage: VoiceTranscriptCommonMessage) {
    if (isSuggestion(transcriptMessage)) {
        return botSuggestionIcon
    }
    if (isCurrentChatTranscriptMessage(transcriptMessage) && transcriptMessage.chatMessageData.channel) {
        switch (transcriptMessage.chatMessageData.channel) {
            case 'sms':
                return smsIcon
            case 'whatsapp':
                return whatsappIcon
            case 'facebook':
                return facebookIcon
            case 'line':
                return lineIcon
            case 'telegram':
                return telegramIcon
            case 'twitter':
                return twitterIcon
            case 'viber':
                return viberIcon
            case 'web':
                return webChatIcon
            case 'wechat':
                return wechatIcon
        }
    }
}
