import {useCallback, useEffect} from 'react'

import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import type {OpenedBotSuggestionsUrls, VoiceTranscriptSuggestion} from '@bpinc/ad-interaction-state-types'
import {isLinkSuggestion} from '@bpinc/ad-interaction-state-types'

import {useLinkOpener} from '../components/external/VoiceTranscript/hooks/useLinkOpener'


export function useBotSuggestionsLinksAutoOpener(
    itemId: string,
    suggestions: VoiceTranscriptSuggestion[] | undefined,
    enabled: boolean,
) {
    const alreadyOpened = useInteractionProp(itemId, s => s?.openedBotSuggestionsUrls)

    const getSuggestionsToOpen = useCallback(
        function getSuggestionsToOpenCallback(
            opened: OpenedBotSuggestionsUrls | undefined,
        ) {
            return suggestions?.filter(
                message =>
                    isLinkSuggestion(message) &&
                    message.autoOpenURL &&
                    !opened?.[message.suggestionURL],
            )
        },
        [suggestions],
    )

    const openLink = useLinkOpener(itemId)

    useEffect(() => {
        openSuggestions()
        function openSuggestions() {
            if (!enabled) return

            const suggestionsToOpen = getSuggestionsToOpen(alreadyOpened)

            suggestionsToOpen?.forEach(openLink)
        }
    }, [alreadyOpened, enabled, getSuggestionsToOpen, openLink])
}
