import {useMemo} from 'react'

import {useAppConfigState} from '@bpinc/ad-app-config-state-context'
import {getCharCount} from '@bpinc/lib-gsm-charset-utils'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useChannelState} from '../../../channel-context'


type MessageCounter = {
    text: string
    enabled: boolean
}

export function useSmsMessageCounter(itemId: string, message: string): MessageCounter {
    const {enableSmsCharacterCount} = useAppConfigState()
    const {t} = useLocalizationState()
    const {getSelectedChannel} = useChannelState()
    const channel = getSelectedChannel(itemId)?.value

    return useMemo(() => {
        if (!enableSmsCharacterCount || channel?.startsWith('sms')) {
            return {enabled: false, text: ''}
        }
        const {msgCount, charCount, charsPerSegment} = getCharCount(message)

        let text: string
        if (msgCount === 1) {
            text = `${charCount}/${charsPerSegment}`
        } else if (charCount < 1000) {
            text = `${charCount} (${t.EXTERNAL_CHAT.sms_messages_number(msgCount)})`
        } else {
            text = String(charCount)
        }
        return {enabled: true, text}
    }, [enableSmsCharacterCount, channel, message, t.EXTERNAL_CHAT])
}
