import {useInteractionChatData} from '@bpinc/ad-interaction-chat-state-context'
import {useInteractionProp} from '@bpinc/ad-interaction-state-context'
import {getInteractionStepId} from '@bpinc/ad-interaction-state-methods'

import {getMessageId} from '../helpers/common'


export function useFirstUnreadMessage(itemId: string) {
    const interactionStepId = useInteractionProp(itemId, getInteractionStepId)
    const chatData = useInteractionChatData(interactionStepId)
    const first = chatData?.messages?.find(m => (m.source === 'customer' || m.source === 'agent') && !m.isRead)
    if (!first) {
        return null
    }

    return getMessageId(first)
}
