import {useEffect, useState} from 'react'

import {useResizeObserver} from '@bpinc/lib-helpers'


export function useHasMultipleLines(contentEditableRef: React.RefObject<HTMLElement>) {
    const [hasMultipleLines, setHasMultipleLines] = useState(false)
    const contentEditableROEntry = useResizeObserver(contentEditableRef)

    useEffect(() => {
        if (contentEditableROEntry) {
            const lineHeight = computeLineHeight(
                contentEditableROEntry.target,
            )
            const height = computeHeight(contentEditableROEntry.target)

            setHasMultipleLines(height > lineHeight)
        }

        function computeLineHeight(element: Element) {
            const result =
              parseFloat(window.getComputedStyle(element).lineHeight) ||
              0
            return isNaN(result) ? 0 : result
        }

        function computeHeight(element: Element) {
            return element.clientHeight
        }
    }, [contentEditableROEntry])

    return hasMultipleLines

}
