import {useCallback} from 'react'

import {useContactsState} from '@bpinc/ad-contacts-state-context'
import {useFormatting} from '@bpinc/ad-formatting-context'
import {formatDate} from '@bpinc/ad-formatting-helpers'
import {useInteraction} from '@bpinc/ad-interaction-state-context'
import {useSessionState} from '@bpinc/ad-session-context'
import {useUserDetailsState} from '@bpinc/ad-user-details-state-context'
import {useNonAssertedInteractionId} from '@bpinc/lib-interaction-context'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {
    getDispositionText,
    getExternalChatMessageDisplayName,
    getHistoryOrChatMessageText,
    getHtmlMessageText,
} from '../../../helpers'
import type {ChatCommonMessagesType} from '../../../types'
import {
    isActivityMessageGroup,
    isChatMessage,
    isExternalChatMessageDate,
    isHistoryMessage,
    isVoiceTranscriptMessage,
} from '../../../types'


export function useGetFormattedClipboardMessage() {
    const {t} = useLocalizationState()
    const {formatFullName} = useFormatting()
    const contacts = useContactsState('contacts')
    const users = useUserDetailsState('userDetails')
    const interactionId = useNonAssertedInteractionId()
    const interaction = useInteraction(interactionId)
    const sessionUser = useSessionState(['firstName', 'lastName'])


    return useCallback(
        function getFormatClipboardMessage(
            message: ChatCommonMessagesType,
        ) {
            const messageData = getMessageData()

            if (!messageData) {
                return ''
            }

            return `[${messageData.date}] ${messageData.name}\n\n${messageData.text}\n\n`


            function getMessageData() {
                if (isExternalChatMessageDate(message)) {
                    return
                }

                if (isVoiceTranscriptMessage(message)) {
                    const {endTime, partyId, text: messageText} = message
                    const callParty = interaction?.parties?.find(party => party.id === partyId)
                    const chartParty = interaction?.chatParties?.find(party => party.party_id === partyId)

                    const name = callParty
                        ? formatFullName(callParty.firstName, callParty.lastName)
                        : chartParty
                            ? formatFullName(chartParty.first_name, chartParty.last_name)
                            : formatFullName(sessionUser.firstName, sessionUser.lastName)

                    const text = getHtmlMessageText(messageText)

                    return {
                        date: formatDate(endTime, {full: true}),
                        name,
                        text,
                    }
                }

                if (isActivityMessageGroup(message)) {
                    const {timestamp, lastName, firstName, notes} = message
                    const text = notes || getDispositionText(message, t, formatFullName)

                    return {
                        date: formatDate(timestamp, {full: true}),
                        name: formatFullName(firstName, lastName),
                        text,
                    }
                }

                if (isHistoryMessage(message) || isChatMessage(message)) {
                    const {timestamp, contactId, userId} = message
                    const displayName = getExternalChatMessageDisplayName(message, formatFullName)
                    const contactData = contactId ? contacts[contactId]?.data : userId ? users[userId]?.data : undefined
                    const userOrContactName = contactData
                        ? formatFullName(contactData.firstName, contactData.lastName)
                        : ''
                    const name = displayName || userOrContactName

                    const text = getHistoryOrChatMessageText(message, t)

                    if (!text) {
                        return
                    }

                    return {
                        date: formatDate(timestamp, {full: true}),
                        name,
                        text,
                    }
                }

                return null
            }
        }, [contacts, formatFullName, interaction?.chatParties, interaction?.parties, sessionUser.firstName, sessionUser.lastName, t, users])
}
