import type {ReactElement} from 'react'
import React from 'react'

import type {VoiceTranscriptCommonMessage} from '@bpinc/ad-interaction-state-types'
import {formatDate, useInnerHTML} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SelectionItem, Sentiment, Svg} from '@bpinc/lib-ui-elements'

import {getMessageId} from '../../../../helpers'
import type {Direction} from '../../../../types'
import {WithHoverOverActions} from '../../ChatMessagesActions'
import {useLinkOpener} from '../hooks/useLinkOpener'
import {getTranscriptMessageIcon} from './utils'

import styles from './Message.module.less'


export type MessageProps = MessageAdditionalProps & {
    message: VoiceTranscriptCommonMessage
}

export interface MessageAdditionalProps {
    itemId: string
    direction: Direction
    closeButton?: ReactElement
    content: string
    isHtml: boolean
    isLink: boolean
}

export const Message = React.forwardRef<HTMLDivElement, MessageProps>(({
    message,
    itemId,
    direction,
    closeButton,
    isLink,
    isHtml,
    content,
}, ref) => {
    const {
        endTime,
        sentiment,
        chatMessageData,
    } = message

    //Already sanitized in useVoiceTranscriptMessagesContentMap
    const innerHTML = useInnerHTML(content)

    const iconSrc = getTranscriptMessageIcon(message)
    const messageId = getMessageId(message)

    const openLink = useLinkOpener(itemId)

    const bubbleElement = (
        <SelectionItem
            ref={ref}
            itemId={messageId}
            focusOnSelection
            touchedOnly
            tabIndex={0}
            data-message-id-selection={messageId}
            className={tcn(styles.message, styles[direction], {
                [styles.html]: isHtml,
                [styles.link]: isLink,
                [styles.closable]: !!closeButton,
                [styles.error]: chatMessageData?.displayType === 'error',
            })}
        >
            <WithHoverOverActions messageId={messageId}>
                <div
                    dangerouslySetInnerHTML={innerHTML}
                    onClickCapture={evt => {
                        if (isLink) {
                            evt.preventDefault()
                            evt.stopPropagation()
                            openLink(message)
                        }
                    }}
                />
            </WithHoverOverActions>
            {closeButton}
        </SelectionItem>
    )

    return (
        <div className={tcn(styles.messageRow, styles[direction])}>
            <div className={tcn(styles.time, styles[direction])}>{formatDate(endTime, 'h:mm A')}</div>
            {bubbleElement}
            {iconSrc && <Svg src={iconSrc} className={tcn(styles.icon, styles[direction])} />}
            {sentiment && (
                <Sentiment sentiment={sentiment} className={tcn(styles.sentiment, styles[direction])} />
            )}
        </div>
    )
})
