import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SelectionItem} from '@bpinc/lib-ui-elements'

import styles from '../../../../styles/Chat.module.less'


interface Props {
    className?: string
    displayName?: string
    isError?: boolean
    isOutgoing?: boolean
    messageId?: string
    sendTime?: number
    showName?: boolean
    showTime?: boolean
}

export const MessageContent: React.FC<React.PropsWithChildren<Props>> = props => {
    const {children, className, isError, messageId} = props

    const combinedClassName = tcn(className, {
        [styles.errorMessageContent]: isError,
    })

    return (
        <SelectionItem
            data-message-id-selection={messageId}
            focusOnSelection
            touchedOnly
            tabIndex={0}
            className={combinedClassName}
            itemId={messageId}
        >
            {children}
        </SelectionItem>
    )
}
